.page__hero.production__hero {
  margin-bottom: 0; }

.production__slider {
  .section__title {
    @include cMLeftIn;
    @include r(1023) {
      margin-left: 0px;
      margin-bottom: 54px; } }
  .slider {
    display: flex;
    overflow: hidden;
    position: relative;
    @include r(1023) {
      display: block;
      position: relative; }
    @include r(767) {
      margin: 0 -20px; }
    &__dots {
      display: block;
      position: static;
      flex: 1 0 320px;
      @include h4; }
    &__dot {
      display: block;
      opacity: .5;
      &.active {
        opacity: 1; }
      &:before {
        display: none; }
      @include r(1023) {
        opacity: 0;
        visibility: hidden;
        color: #ffffff;
        position: absolute;
        z-index: 2;
        left: 30px;
        top: 30px;
        transition: .3s;
        cursor: default;
        &.active {
          opacity: 1;
          visibility: visible; } }
      @include r(767) {
        left: 20px;
        top: 20px;
        max-width: 160px; } }
    &-inner {
      flex: 1 0 calc(100% - 320px);
      width: calc(100% - 320px);
      @include r(1023) {
        width: 100%; } }
    @include owlNavFlex;
    .slider__counter {
      top: 30px;
      right: 30px;
      @include r(767) {
        top: 20px !important; } }
    .owl {
      &-dots {
        display: none; }
      &-nav {
        right: 30px;
        left: auto;
        top: auto;
        bottom: 30px;
        @include r(767) {
          display: none; } }
      &-item {
        margin-right: 0 !important;
        img {
          object-fit: cover;
          width: 100%; }
        svg {
          fill: #fff; }
        &:hover {
          svg {
            fill: $orangeish_h; } }
        &:active {
          svg {
            fill: $orangeish_a; } } }
      &-carousel {
        overflow: hidden;
        img {
          width: 100%; } } } } }

.look-news {
  display: flex;
  justify-content: space-between;
  @include cMLeftIn;
  &__right {
    flex: 1 0 160px;
    margin-left: 160px;
    //max-width: 160px
    @include r(1439) {
      margin-left: 32px; }
    @include r(767) {
      margin-left: 0;
      margin-top: 24px; } }
  @include r(1023) {
    margin-left: 0 !important; }
  @include r(767) {
    display: block;
    .video {} } }



.production__lab {
  background-color: $light-gray;
  padding: 100px 0;
  position: relative;
  min-height: 900px;
  height: 100vh;
  @include r(1439) {
    min-height: 768px;
    padding: 50px 0;
    .h2 {
      max-width: 330px; } }
  @include r(1023) {
    min-height: 0;
    height: 678px;
    .slider__counter {
      top: 65px;
      right: 30px; } }
  @include r(767) {
    height: auto;
    padding-bottom: 0;
    .slider__counter {
      display: none; } }
  @include owlNavFlex;
  .owl {
    &-carousel {
      @include absoluteFill;
      @include r(767) {
        position: static; } }
    &-nav {
      bottom: 60px;
      @include cRight;
      @include r(767) {
        position: static;
        height: 0; } }
    @include r(767) {
      &-prev,
      &-next {
        top: auto;
        bottom: 130px;
        position: absolute; }
      &-prev {
        left: 20px; }
      &-next {
        left: auto;
        right: 20px; } }
    &-item {
      min-height: 900px;
      height: 100vh;
      @include r(1439) {
        min-height: 768px;
        height: auto; }
      @include r(1023) {
        min-height: 678px; }
      @include r(767) {
        min-height: 0;
        height: auto; }
      img {
        position: absolute;
        left: 46.5%;
        top: 0;
        bottom: 0;
        height: 100%;
        width: auto;
        @include r(1023) {
          left: 52%; }
        @include r(767) {
          position: static;
          transform: translate(0);
          max-width: 60%;
          height: auto;
          margin: 36px auto 0 auto; } } } }
  &-slider {
    &__info {
      position: absolute;
      max-width: 645px;
      width: 43%;
      @include r(767) {
        margin-top: 25px; }
      @include cLeft;
      bottom: 67px;
      @include r(1023) {
        max-width: 343px;
        bottom: 40px; }
      @include r(767) {
        position: static;
        width: 100%; } }
    &__descr {
      @include h4;
      margin-top: 30px;
      @include r(767) {
        display: none; } }
    &__item {
      height: 100vh;
      position: relative;
      @include r(1023) {
        height: auto;
        min-height: 678px; }
      @include r(767) {
        min-height: 0; } }
    .owl-prev {
      margin-right: 0; } } }
.production-suppliers {
  .flex {
    justify-content: space-between;
    @include r(767) {
      display: block; } }
  .section__title {
    @include cMLeftIn;
    @include r(767) {
      margin-left: 0; } }
  &__left {
    flex: 1 0 320px;
    @include r(1439) {
      flex: 1 0 236px; } }
  &__descr {
    max-width: 240px;
    @include r(1439) {
      max-width: 100px; }
    @include r(767) {
      max-width: 211px; } }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @include r(767) {
      margin-top: 40px; } }
  &__item {
    margin-bottom: 70px;
    flex: 0 1 33.33%;
    @include r(1439) {
      max-width: 28%;
      img {
        max-width: 100%;
        max-height: 100%; } }
    @include r(767) {
      flex: 0 1 50%;
      max-width: none;
      margin-bottom: 36px;
      img {
        max-width: 80%;
        max-height: 35px; } } }
  &__right {
    & .hr {
      margin-top: 0; } } }

.production-materials-slider {
  height: 100vh;
  min-height: 600px;
  @include r(1439) {
    min-height: 0;
    height: auto; }
  @include owlNavFlex;
  .owl {
    &-item {
      height: 100vh;
      min-height: 600px;
      @include r(1439) {
        height: auto;
        max-height: none;
        min-height: 0; } }
    &-prev,
    &-next {
      @include hTop;
      bottom: auto;
      @include r(767) {
        top: 0;
        bottom: 0; } }
    &-prev {
      @include cLeft;
      @include r(767) {
        left: 20px; } }
    &-next {
      @include cRight;
      left: auto;
      @include r(767) {
        right: 20px; } } }
  &__item {
    height: 100%;
    @include r(1439) {
      height: auto;
      background-image: none !important; }
    img {
      display: none !important;
      @include r(1439) {
        display: block !important; } }
    @include bgcc;
    &__info {
      background-color: #fff;
      bottom: 0;
      min-height: 50%;
      min-width: 50%;
      width: auto;
      left: 50%;
      right: 0;
      position: absolute;
      padding: 82px;
      @include r(1439) {
        position: static; }
      @include r(767) {
        display: none; } }

    &__title {
      @include h3;
      margin-bottom: 40px; }
    &__descr {
      @include h4; } } }
.party-ready-prod {
  background-color: $light-gray;
  padding: 100px 0;
  @include r(767) {
    padding: 50px 0; } }
.img-parallax-sizing {
  font-size: 0;
  display: flex;
  align-items: flex-start;
  @include r(767) {
    display: block; }
  &__item {
    flex: 0 1 50%;
    margin-right: 50px;
    &:last-child {
      margin-right: 0;
      flex: 0 1 100%; }
    @include r(767) {
      margin: 0 -20px !important;
      width: auto; } }
  img {
    height: auto;
    width: 100%;
    &:last-child {
      margin-right: 0; } } }
.painting-steel {
  color: #ffffff;
  height: 100vh;
  @include r(1023) {
    height: 576px; }
  .video {
    &__cover {
      padding: 100px 0;
      background-color: $sapphire;
      display: flex;
      justify-content: space-between;
      @include r(1439) {
        padding: 75px 0; }
      @include r(1023) {
        padding: 50px 0; } } }
  .container {
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    @include fontStandart; }
  &__items {
    display: flex;
    justify-content: space-between;
    @include r(767) {
      display: block; }
    &:after {
      content: ''; } }
  &__item {
    @include r(1023) {
      max-width: 250px; }
    @include r(767) {
      margin-bottom: 22px; }
    &__title {
      @include h3;
      margin-bottom: 15px;
      color: #ffffff;
      display: block;
      @include r(767) {
        margin-bottom: 10px; } }
    a {
      @include trans1;
      &:hover {
        color: $orangeish_h; }
      &:active {
        color: $orangeish_a; } } }
  .img-orange-roll {
    position: absolute;
    left: 170px;
    right: 170px;
    bottom: 0;
    height: auto;
    font-size: 0;
    max-width: 1150px;
    margin: auto;
    @include r(1023) {
      left: 90px;
      right: 90px; }
    @include r(767) {
      left: 0;
      right: 0; }
    img {
      width: 100%;
      height: auto; } } }

