@keyframes mainNewsSliderNextAnimate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.mainNews {
  .simpleFooter {
    position: absolute;
    left: 0;
    right: 0;
    top: auto;
    bottom: 0;
    z-index: 2;
    opacity: .5;
  }

  .main__screen__header {
    bottom: 80px;
  }

  .slider__counter {
    display: none;
  }
}

.mainNewsSlider {
  @include absoluteFill;

  .owl-dots {
    width: 64px;
    position: absolute;
    top: calc(50% + 56px);
    bottom: 0;
    right: 80px;
    margin: auto;
  }

  .owl-dot {
    width: 8px;
    height: 8px;
    margin: 0 auto 12px auto;
    background-color: #fff;
    border-radius: 9px;
    display: block;
    @include trans1;

    &.active {
      background-color: $orangeish;
    }
  }
}

.mainNewsSliderNext {
  width: 64px;
  height: 64px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 80px;
  z-index: 2;
  cursor: pointer;
  border-radius: 99px;
}

.mainNewsSliderNextBorder {
  border-radius: 99px;
  overflow: hidden;
  @include absoluteFill;

  &.animate {
    animation-name: mainNewsSliderNextAnimate;
    animation-duration: 7000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }

  &:before,
  &:after {
    content: '';
    width: 100%;
    height: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
  }

  &:before {
    top: auto;
    background-image: linear-gradient(right, $orangeish, rgba(255, 142, 75, 0.5));
  }

  &:after {
    bottom: auto;
    background-image: linear-gradient(right, rgba(255, 142, 75, 0), rgba(255, 142, 75, 0.5));
  }
}

.mainNewsSliderNextImage {
  $indent: 3px;
  position: absolute;
  top: $indent;
  bottom: $indent;
  left: $indent;
  right: $indent;
  background-position: center;
  background-size: cover;
  overflow: hidden;
  border-radius: 99px;

  //noinspection CssUnknownTarget
  &:after {
    content: '';
    @include absoluteFill;
    left: 2px;
    background: url('/icons/rightWhite.svg') no-repeat center;
  }
}

.mainNewsSliderItem {
  padding: 120px 194px 80px 400px;
  height: 100vh;

  .btn-next {
    color: #fff !important;
  }

  .btn-next__wrapper {
    margin-top: 70px;
  }
}

.mainNewsSliderItemInfo {
  max-width: 648px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  overflow: hidden;
}

.mainNewsSliderItemBlock {
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.mainNewsSliderItemTitle {
  margin-bottom: 22px;
}

.mainNewsSliderItemDescription {
  overflow: hidden;
  flex: 0 1 auto;
}

.mainNewsSliderItemDate {
  font-size: 30px;
  margin-bottom: 50px;
  @include futura;
}

@include small-viewport {
  .mainNews {
    .mainNewsSliderNext,
    .owl-dots {
      right: 40px;
    }
  }

  .mainNewsSliderItem {
    padding-left: 274px;

    .btn-next__wrapper {
      margin-top: 64px;
    }
  }

  .mainNewsSliderItemTitle {
    margin-bottom: 32px;
  }
}

@include tablet-viewport {
  .main__screen.mainNews {
    .mainNewsSliderNext,
    .owl-dots {
      right: 24px;
    }

    .owl-dots {
      display: block;
    }
  }

  .mainNewsSliderItem {
    padding: 300px 110px 100px 200px;
  }
}

@include mobile-viewport {
  .mainNews {
    height: auto;
    padding-top: 0 !important;

    .owl-carousel {
      position: static;

      .owl-nav {
        top: 115px !important;
      }
    }

    .slider__counter {
      top: 115px !important;
      display: flex;
    }

    .container-wrapper {
      padding: 0;
    }

    .main__screen__header {
      position: absolute;
      left: 20px;
      top: 32px;
      bottom: auto;
    }
  }

  .mainNewsSliderItem {
    position: static;
    padding: 168px 20px 160px 20px;
    height: auto;

    .btn-next__wrapper {
      margin-top: 24px;
    }
  }

  .mainNewsSliderItemInfo,
  .mainNewsSliderItemBlock {
    overflow: visible;
  }

  .mainNewsSliderItemDate {
    font-size: 26px;
    margin-bottom: 38px;
  }

  .mainNewsSliderItemTitle {
    margin-bottom: 18px;
  }

  .mainNewsSliderItemDescription {
    font-size: 16px;
    overflow: visible;
  }

  .mainNewsSliderNext,
  .owl-dots {
    display: none;
  }
}
