.site-nav {
  z-index: 2;
  &__back,
  &__next {
    @include cLeft;
    @include h4;
    color: #000000;
    position: absolute;
    z-index: 2;
    @include trans1;
    .icon {
      font-size: 19px;
      vertical-align: middle;
      margin-right: 5px;
      @include trans1; }
    span {
      vertical-align: middle; }
    &:hover {
      color: $orangeish_h !important;
      .icon {
        fill: $orangeish_h !important; } } }
  &__next {
    @include cBot;
    @include r(767) {
      display: none; } }
  &__back {
    top: 112px;
    @include mobile-viewport {
      top: 90px; }
    .icon {
      transform: rotate(180deg);
      @include r(767);
      margin-right: 0; }
    @include r(767) {
      font-size: 0; } }
  &.static {
    @include hMTop;
    @include r(767) {
      margin-bottom: 20px; }
    .site-nav {
      &__next,
      &__back {
        position: static; } } } }
