.main__screen__header {
  font-size: size-viewport-height(110, 900);
  writing-mode: vertical-lr;
  transform: scale3d(-1, -1, 1);
  position: absolute;
  z-index: 2;
  left: 80px;
  bottom: 60px;
  @include futura;
  font-weight: 500;

  @include small-viewport {
    font-size: size-viewport-height(72, 768);
    left: 120px;
  }

  @include tablet-viewport {
    writing-mode: lr;
    transform: none;
    font-size: 72px;
    top: 80px;
    left: 200px;
    bottom: auto;
  }

  @include mobile-viewport {
    writing-mode: lr;
    transform: none;
    position: static;
    font-size: 48px;
  }
}


.main__screen.main__catalog {
  .main__screen__header {
    @include tablet-viewport {
      left: 0;
      right: 0;
      text-align: center;
      margin: auto;
    }
  }
}
