@include font('Futura PT', 'Futura PT Medium', 600);
@include font('ProximaNova', 'ProximaNova-Regular');
@include font('ProximaNova', 'ProximaNova-RegularIt', normal, italic);
@include font( 'ProximaNova', 'ProximaNova-Semibold', 600);
@include font( 'ProximaNova', 'ProximaNova-SemiboldIt', 600, italic);
@include font( 'ProximaNova', 'ProximaNova-Bold', bold);


@mixin proxima {
  font-family: 'ProximaNova',sans-serif; }
@mixin futura {
  font-family: 'Futura PT',sans-serif;
  font-weight: 600; }

//=fMed
//  font-family: 'Futura PT Medium',sans-serif
//=pReg
//  font-family: 'ProximaNova-Regular',sans-serif
//=pRegIt
//  font-family: 'ProximaNova-RegularIt',sans-serif
//=pSemi
//  font-family: 'ProximaNova-Semibold',sans-serif
@mixin pSemiIt {
  font-style: italic;
  font-weight: 600; }
@mixin fontStandart {
  font-size: 18px;
  line-height: 1.33;
  @include r(767) {
    font-size: 16px; } }

@mixin h0 {
  font-size: 220px;
  @include futura;
  @include r(1439) {
    font-size: 110px; }
  @include r(767) {
    font-size: 48px; } }

@mixin h1 {
  font-size: 110px;
  line-height: 0.95;
  @include futura;
  @include r(1439) {
    font-size: 72px; }
  @include r(767) {
    font-size: 44px; } }
@mixin h2 {
  font-size: 72px;
  line-height: 1.14;
  @include futura;
  @include r(1439) {
    font-size: 56px; }
  @include r(767) {
    font-size: 36px; } }
@mixin h3 {
  font-size: 46px;
  line-height: 1.13;
  @include futura;
  @include r(1439) {
    font-size: 36px; }
  @include r(767) {
    font-size: 28px; } }
@mixin h4 {
  font-size: 24px;
  line-height: 1.42;
  @include r(767) {
    font-size: 20px; } }

.big-title {
  @include h0;
  @include futura; }
.h1 {
  @include h1; }
.h2 {
  @include h2; }
.h3 {
  @include h3; }
.h4 {
  @include h4; }

$i: 1;
.h1,
.h2,
.h3 {
  @include futura; }
.h4 {
  font-weight: normal; }

.h5 {
  margin-bottom: 83px; }
h1 {
  @include h1; }
h2 {
  @include h2; }
h3 {
  @include h3; }
.descr {
  max-width: 620px;
  margin-left: auto;
  margin-right: auto; }
p {
  margin-bottom: 40px; }
p.h4 {
  margin-bottom: 60px; }
