.siteNavigation {
  position: absolute;
  left: 80px;
  top: 120px;
}

.siteNavigation-linkBack {
  display: flex;
  align-items: center;
  font-size: 24px;
  margin-bottom: 100px;
  color: #000;

  svg {
    margin-right: 16px;
    font-size: 16px;
    transform: rotate(90deg);
  }
}

.breadCrumbs {
  max-width: 140px;
}

.breadCrumbs-slash {
  opacity: .5;
}

.breadCrumbs-link {
  @include trans1;

  opacity: .5;
  color: #000;

  &:hover {
    opacity: .7;
  }
}

@include small-viewport {
  .siteNavigation-linkBack {
    margin-bottom: 70px;
  }
}

@include tablet-viewport {
  .siteNavigation {
    top: 110px;
    left: 60px;
  }

  .siteNavigation-linkBack {
    margin-bottom: 30px;
  }
}

@include mobile-viewport {
  .siteNavigation {
    top: 74px;
    left: 20px;
    right: 20px;
  }

  .siteNavigation-linkBack {
    margin-bottom: 30px;
    font-size: 20px;
  }

  .breadCrumbs {
    max-width: none;

    > div {
      display: inline;
    }
  }

  .breadCrumbs-slash {
    margin: 0 0.35em;
  }
}
