.tabs {
  &__dots {
    display: flex;
    justify-content: space-between;
    border-bottom: solid 1px #e9e9e9;
    @include r(767) {
      display: block;
      min-height: 54px;
      position: relative;
      padding-top: 54px;
      cursor: pointer;
      .tabs__dot {
        padding: 0;
        height: 0;
        border: 0 !important;
        opacity: 0;
        text-align: center;
        transition: .3s;
        &.active {
          display: flex;
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          border-bottom: 0;
          cursor: pointer;
          opacity: 1;
          height: 54px; } }
      &.active {
        .tabs__dot {
          display: flex;
          height: 54px;
          opacity: 1;
          &.active {
            &:after {
              transform: rotate(180deg); } } } } }
    &-others {
      position: relative;
      display: flex;
      align-items: center;
      z-index: 2;
      &__btn {
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        svg {
          height: 1em;
          font-size: 3px;
          fill: $sapphire; }
        &:hover {
          svg {
            fill: $sapphire_h; } }
        &:active {
          svg {
            fill: $sapphire_a; } } }
      &-inner {
        position: absolute;
        top: 59px;
        right: 15px;
        background-color: #fff;
        box-shadow: 0 -8px 39px 0 rgba(0, 0, 0, .1);
        padding: 20px;
        min-width: 280px;
        opacity: 0;
        visibility: hidden;
        transition: .3s;
        &.active {
          opacity: 1;
          visibility: visible; }
        .tabs__dot {
          display: inline-block; }
        &:after {
          content: '';
          $b: 18px solid transparent;
          border-left: $b;
          border-bottom: 18px solid #fff;
          position: absolute;
          right: 0;
          top: 0;
          transform: translate(0, -100%);
          transition: .3s; } } } }
  &__dot {
    color: rgba(#000, .5);
    border-bottom: 2px solid transparent;
    padding: 17px 0;
    cursor: pointer;
    @include trans1;
    &:hover {
      color: #000000; }
    @include r(767) {
      display: flex;
      justify-content: center;
      align-items: center; }
    &.active {
      color: #000000;
      border-bottom-color: $sapphire;
      cursor: default;
      @include r(767) {
        position: relative;
        &:after {
          background-image: url(data:image/svg+xml;charset=utf-8;base64,);
          display: inline-block;
          margin-left: 10px;
          width: 6px;
          height: 3px;
          content: ''; } } } }

  &__items {
    position: relative; }
  &__item {
    display: none;
    opacity: 0;
    &.active {
      display: block;
      opacity: 1; } } }

.section.last-form {
  padding-bottom: 0 !important; }

.contacts {
  &__table {
    border-spacing: 0;
    @include r(1023) {
      display: block;
      margin-top: 55px; }
    td {
      border-bottom: solid 1px #e9e9e9;
      padding: 50px 0;
      vertical-align: middle;
      @include r(1023) {
        display: block;
        padding: 0;
        border-bottom: 0;
        margin-bottom: 28px;
        &:last-child {
          margin-bottom: 0; } } }
    tr {
      &:last-child td {
        border-bottom: 0; }
      @include r(1023) {
        border-right: solid 1px #e9e9e9;
        flex: 0 1 50%;
        padding-left: 49px;
        display: block;
        &:last-child {
          border-right: 0; }
        &:first-child {
          padding-left: 0; } }
      @include r(767) {
        padding-left: 0;
        border-right: 0;
        border-bottom: solid 1px #e9e9e9;
        padding: 40px 0;
        &:first-child {
          padding-top: 0; }
        &:last-child {
          border-bottom: 0;
          padding-bottom: 0; } } }
    @include r(1023) {
      tbody {
        display: flex;
        justify-content: space-between; } }
    @include r(767) {
      tbody {
        display: block; } } }
  .yandex-map {
    margin-top: 26px;
    @include r(1023) {
      margin-top: 42px; } } }

.yandex-map {
  width: 100%;
  height: 647px; }
