.mainSliderCounter {
  width: 16px;
  position: absolute;
  left: 32px;
  right: 0;
  @include hTop2;
  bottom: 0;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.mainSliderCounter-Counts {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  pointer-events: none;
}

.mainSliderCounter-CountsLine {
  width: 2px;
  height: 40px;
  margin: 20px 0;
  background-color: #000;
}

.in-dark {
  .mainSliderCounter-CountsLine {
    background-color: #fff;
  }
}

.mainSliderCounter-prev {
  top: 0;
  position: absolute;
  z-index: 11;

  svg {
    transform: rotate(90deg);
  }
}

$widthIcons: 54px;

.mainSliderCounter-prev,
.mainSliderCounter-next {
  width: 16px;
  height: $widthIcons;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    flex: 0 0 $widthIcons;
    cursor: pointer;
    @include trans1;

    &:hover {
      fill: $orangeish;
    }
  }
}

.mainSliderCounter-next {
  top: auto;
  bottom: 50px;
  position: absolute;
  z-index: 11;

  svg {
    transform: rotate(-90deg);
  }
}

@include tablet-viewport {
  .mainSliderCounter {
    left: 24px;
  }
}

@include mobile-viewport {
  .mainSliderCounter {
    display: none;
  }
}
