.pic-cover__info {
  &__bg {
    margin: auto;
    right: 100%;
    opacity: 0;
    @include trans; }
  &__inner {
    opacity: 0;
    transform: translate(0, 50px);
    @include trans; }

  &.show {
    .pic-cover__info {
      &__bg {
        right: 0%; }
      &__bg,
      &__inner {
        @include op1tt00; } } } }
.graphic-growth {
  .graphic-growth__col {
    transition: 1s ease;
    > div {
      transition: 1s ease; } }
  &.hide {
    .graphic-growth__col {
      padding-top: 0;
      padding-bottom: 0;
      height: 0 !important;
      > div {
        opacity: 0; }
      @include r(767) {
        height: 130px !important;
        width: 0 !important; } } } }
