.objects {
  &__items {
    display: flex;
    flex-wrap: wrap;
    padding-top: 60px;
    @include r(1023) {
      display: block; }
    @include r(767) {
      padding-top: 20px; } }
  &__item {
    flex: 0 1 50%;
    padding: 160px 40px;
    color: #fff;
    text-align: center;
    position: relative;
    overflow: hidden;
    min-height: 456px;
    height: 30vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @include r(1023) {
      display: block; }
    @include r(767) {
      min-height: 360px;
      padding: 40px;
      display: flex;
      align-items: center;
      justify-content: center; }
    &__title {
      margin-bottom: 20px; }
    &__pic {
      @include absoluteFill;
      @include bgcc;
      @include trans; }
    &__info {
      position: relative; }
    &__descr {
      @include h4; }
    &:hover {
      .objects__item {
        &__pic {
          transform: scale(1.1); } } } } }

.objects-footer {
  border-top: 1px solid #e2e2e2; }
