.partner {
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  margin: 10px;
  @include r(767) {
    width: calc(50% - 10px);
    margin: 5px; }
  &:nth-child(1) {
    grid-column: 1 / 3;
    grid-row: 1 / 3; }
  &:nth-child(2) {
    grid-column: 3 / 5; }
  img {
    width: auto !important;
    max-width: 100%; }
  &__item {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: repeat(2, 25vw);
    margin: -10px;
    @include r(1023) {
      .partner {
        img {
          transform: scale(.7); } } }
    @include r(767) {
      display: flex;
      flex-wrap: wrap;
      .partner {
        img {
          transform: scale(1); } } } } }
.partners {
  &-wrapper {
    position: relative; }
  .owl {
    &-carousel {
      position: static; }
    &-nav {
      position: absolute;
      top: 20px;
      @include cRight;
      display: flex;
      left: auto;
      bottom: auto;
      @include r(767) {
        margin-top: 20px;
        position: static;
        justify-content: center; } } } }
