.objectPage {
  .objectPage-description {
    margin-bottom: 160px;
  }

  .main__review {
    display: block;
    position: static;
    opacity: 1;
    transform: translate(0);
  }

  .link-next {
    margin-top: 0;
  }

  .main__review {
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 50px;
  }
}

@include mobile-viewport {
  .objectPage {
    .objectPage-description {
      margin-bottom: 40px;
    }
  }
}
