.objectPage-description {
  max-width: 775px;
  margin: 0 auto;
}

.objectPage-descriptionHeader {
  margin-bottom: 22px;
}

.objectPage-descriptionItems {
  margin-top: 56px;
  display: flex;
  flex-wrap: wrap;
}

.objectPage-descriptionItem {
  margin-right: 130px;
  margin-bottom: 10px;
}

.objectPage-descriptionItemHeader {
  @include futura;
  font-size: 46px;
  margin-bottom: 16px;
}

@include mobile-viewport {
  .objectPage-descriptionItems {
    margin-top: 20px;
  }

  .objectPage-descriptionHeader {
    margin-bottom: 20px;
  }

  .objectPage-descriptionItemHeader {
    font-size: 32px;
    margin-bottom: 8px;
  }
}
