.productsPage {
  background-color: #eaeaea;
}

.productsPage-list {
  display: flex;
  flex-wrap: wrap;
}

.productsPage-item {
  flex: 0 0 100%;
  $border: 1px solid #c4c4c4;
  border-top: $border;
  padding-top: 64px;

  &.half {
    flex: 0 0 50%;

    &:first-child {
      padding-right: 185px;
    }
  }
}

.productsPage-itemLast {
  width: 100%;
  padding-bottom: 90px;

  .productsPage-item {
    position: relative;
  }

  .productsPage-itemInfo {
    position: absolute;
    left: 44px;
    bottom: 99px;
    display: block;
    max-width: 390px;
    padding: 0;

    .btn-next__wrapper {
      margin-top: 40px;
    }
  }

  .productsPage-itemImage {
    img {
      max-width: none;
    }
  }
}

.productsPage-list-combined {
  $border: 1px solid #c4c4c4;

  width: 100%;
  display: flex;
  border-top: $border;

  .productsPage-itemLinks {
    .btn-next__wrapper {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }

  .productsPage-item {
    border-top: 0;
    flex: 0 0 50%;
    width: 50%;

    &:not(:last-child) {
      border-right: $border;
    }

    &:not(:first-child) {
      padding-left: 60px;
    }
  }

  .productsPage-itemInfo {
    display: block;
    padding-top: 50px;
    max-width: 500px;
    padding-bottom: 64px;
  }

  .productsPage-itemImage {
    img {
      max-width: 464px;
      max-height: 306px;
      margin: 0;
      width: 100%;
    }
  }

  .productsPage-itemLinks {
    margin-top: 40px;
  }
}

.productsPage-itemInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 100px 0 80px;
}

.productsPage-itemLinks {
  display: flex;

  .btn-next__wrapper {
    &:not(:last-child) {
      margin-right: 54px;
    }
  }
}

.productsPage-itemHeader {
  font-size: 46px;
  @include futura;
}

.productsPage-itemImage {
  img {
    width: 100%;
    max-width: 820px;
    margin: 0 auto;
    display: block;
  }
}

@include small-viewport {
  //.productsPage-list-combined,
  .productsPage-list {
    display: block;
  }

  .productsPage-list-combined {
    .productsPage-item {
      &:first-child {
        padding-right: 40px;
      }
      &:not(:first-child) {
        padding-left: 40px;
      }
    }
  }

  .productsPage-list {
    .productsPage-item {
      border-top: 1px solid #c4c4c4;

      &:nth-last-child(2) {
        .productsPage-itemImage {
          img {
            max-width: none;
          }
        }
      }

      .productsPage-itemImage {
        img {
          max-width: 576px;
          max-height: none;
          margin: 0 auto;
        }
      }

      .productsPage-itemInfo {
        max-width: none;
        display: block;
      }
    }

    .productsPage-itemHeader {
      max-width: none;
    }

    .productsPage-itemLinks {
      margin-top: 42px;
    }
  }

  .productsPage-itemLast {
    .productsPage-itemInfo {
      left: 16px;
      bottom: 24px;
    }

    .productsPage-item {

      .productsPage-itemHeader {
        max-width: 390px;
      }

      .productsPage-itemLinks {
        margin-top: 0;
      }

      .productsPage-itemImage {
        img {
          max-width: none !important;
        }
      }
    }
  }
}

@include r(1023) {

  .productsPage-list-combined {
    display: block;

    .productsPage-item {
      border-right: 0;
      width: 100%;
      padding-left: 0 !important;
      padding-right: 0 !important;
    }
  }

  .productsPage-list {
    //noinspection CssOptimizeSimilarProperties
    .productsPage-item {
      $border: 1px solid #c4c4c4;
      border-top: $border;
      border-right: 0 !important;

      .productsPage-itemImage {
        img {
          max-width: 575px !important;
          margin: 0 auto;
          max-height: none;
        }
      }

      .productsPage-itemInfo {
        display: block;
        max-width: none;

        .productsPage-itemLinks {
          margin-top: 40px;
        }
      }

      &:nth-last-child(2) {
        .productsPage-itemImage {
          img {
            max-width: none;
          }
        }
      }
    }

    .productsPage-itemLast {
      .productsPage-item {
        padding-top: 170px;
      }

      .productsPage-itemInfo {
        left: 0;
        bottom: 0;
      }

      .productsPage-itemHeader {
        max-width: 390px;
      }

      .btn-next__wrapper {
        margin-top: 0;
      }

      .productsPage-itemImage {
        transform: translate(40px, -60px) scale(1.2);
      }
    }
  }

  .productsPage-itemInfo {
    padding-bottom: 70px;
  }
}

@include mobile-viewport {
  .productsPage-item {
    padding-top: 60px !important;

    .productsPage-itemHeader {
      font-size: 28px;
    }

    .productsPage-itemInfo {
      padding-top: 28px;
      padding-bottom: 60px;
    }

    .productsPage-itemLinks {
      display: block;
      margin-top: 30px !important;

      .btn-next__wrapper {
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }

  .productsPage-itemLast {
    padding-bottom: 20px;

    .productsPage-itemInfo {
      position: static;
    }

    .productsPage-itemImage {
      transform: translate(0) !important;
    }
  }
}
