.servicesPage-item {
  padding: 248px 0 64px;
  position: relative;
  display: flex;

  &:nth-child(even) {
    justify-content: flex-end;
  }
}

.servicesPage-itemImage {
  @include absoluteFill;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.servicesPage-itemInfo {
  position: relative;
  background-color: #fff;
  padding: 60px 54px;
  max-width: 640px;
}

.servicesPage-itemHeader {
  font-size: 72px;
  @include futura;
  margin-bottom: 32px;
  line-height: 70px;
}

.servicesPage-itemAnnotate {
  font-size: 24px;
  margin-bottom: 24px;
}

@include small-viewport {
  .servicesPage-itemHeader {
    font-size: 56px;
    line-height: 60px;
    margin-bottom: 24px;
  }
}

@include r(1023) {
  .servicesPage-item {
    display: block;
    padding: 0;

    .container-wrapper {
      padding: 0;
    }
  }

  .servicesPage-itemImage {
    position: static;

    img {
      width: 100%;
      height: auto;
      object-fit: none;
    }
  }

  .servicesPage-itemInfo {
    max-width: none;
    padding: 40px 30px 60px 30px;
  }

  .servicesPage-itemHeader {
    font-size: 56px;
    line-height: 60px;
  }
}

@include mobile-viewport {
  .servicesPage-itemInfo {
    padding: 24px 20px 32px 20px;
  }

  .servicesPage-itemHeader {
    font-size: 36px;
    line-height: 40px;
    margin-bottom: 24px;
  }

  .servicesPage-itemAnnotate {
    font-size: 20px;
    margin-bottom: 35px;
  }
}
