.header {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: 100;
  &:before {
    transition: .3s ease-out;
    content: '';
    @include absoluteFill;
    background-color: $sapphire;
    z-index: -1;
    left: 100%;
    opacity: 0; }
  .phone {
    .icon {
      display: none; }
    @include r(767) {
      font-size: 0;
      .icon {
        font-size: 20px;
        display: block; } } }
  @include r(1023) {
    height: 70px; }
  @include r(767) {
    height: 50px; }
  &.in-light {
    color: #000000;
    .icon-phone {
      stroke: #000; }
    a {
      color: #000000; }
    .header {
      &__location {
        cursor: pointer;
        @include hoverOpacity;
        .icon {
          fill: #000; }
        .icon-small-arrow {
          border-top-color: #000;
          @include r(767) {
            display: none; } } }
      &__search-btn {
        & .icon {
          stroke: #000000;
          @include trans1; } }
      &__nav {
        a {
          &:before {
            background-color: $sapphire; } } } } }
  &__inner {
    padding: 0 38px;
    flex: auto;
    height: 100%;
    @include r(1023) {
      padding: 0 30px; }
    @include r(767) {
      padding: 0; } }
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    a {
      @include hoverOpacity; } }
  &__left,
  &__right {
    height: 100%;
    display: flex;
    align-items: center; }
  &__logo {
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-color: $orangeish;
    @include trans1;
    @include r(1023) {
      width: 70px; }
    @include r(767) {
      width: 50px; }
    &:hover {
      background-color: $orangeish_h; }
    &:active {
      background-color: $orangeish_a; }
    .icon {
      font-size: 43px;
      fill: #fff;
      @include r(1023) {
        font-size: 38px; }
      @include r(767) {
        font-size: 25px; } } }
  &__location {
    display: flex;
    align-items: center;
    &__name {
      margin: 0 11px;
      @include r(767) {
        display: none; } }
    &.active {
      color: #ffffff;
      .icon-small-arrow {
        transform: rotate(180deg);
        border-top-color: #fff !important; }
      .icon {
        fill: #fff !important; } } }
  &__language {
    margin: 0 22px;
    cursor: pointer;
    @include hoverOpacity; }
  &__search {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    display: none;
    opacity: 0;
    @include r(767) {
      width: calc(100% - 30px);
      margin: 0 auto;
      input {
        padding-right: 10px;
        width: 100%; } }
    &:after {
      position: absolute;
      left: 100%;
      right: 0;
      bottom: 0;
      border-bottom: solid 1px rgba(#fff, 0.3);
      content: '';
      @include trans; }
    &.active {
      opacity: 1;
      &:after {
        left: 0; } }

    &__input {
      color: #ffffff;
      height: 100%;
      font-weight: normal;
      background-color: transparent;
      flex: auto;
      &::placeholder {
        color: rgba(#fff, 0.5); } }
    &__close {
      font-size: 0;
      cursor: pointer;
      .icon {
        fill: #ffffff;
        font-size: 19px; } }

    &-btn {
      cursor: pointer;
      font-size: 0;
      @include hoverOpacity;
      & .icon {
        stroke: #fff;
        font-size: 23px;
        width: 22px;
        height: 23px;
        use {
          width: 22px !important;
          height: 23px !important; } } } }
  &__nav {
    display: flex;
    align-items: center;
    height: 100%;
    @include r(1350) {
      display: none; }
    a {
      margin-right: 30px;
      position: relative;
      height: 100%;
      display: flex;
      align-items: center;
      @include trans1;
      &.active {
        &:before {
          position: absolute;
          content: '';
          height: 3px;
          background-color: #fff;
          left: 0;
          right: 0;
          top: 0; } } } }
  &__hamburger {
    background-color: $sapphire;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 100%;
    @include r(1023) {
      width: 70px; }
    @include r(767) {
      width: 50px; }
    @include trans1;
    &:hover {
      background-color: $sapphire_h; }
    &:active {
      background-color: $sapphire_a; }

    .icon {
      width: 27px;
      height: 20px;
      position: absolute;
      @include r(767) {
        width: 22px;
        height: 18px; }
      .line {
        height: 2px;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        background-color: #fff;
        @include trans1;
        &1 {
          top: 0; }
        &3 {
          bottom: 0; }
        &2 {
          top: 0;
          bottom: 0;
          margin: auto; } } }
    &.active {
      .line {
        transform: rotate(45deg);
        top: 0;
        bottom: 0;
        left: 0;
        top: 0;
        margin: auto;
        &2 {
          opacity: 0; }
        &3 {
          transform: rotate(-45deg); } } } }

  &.in-dark,
  &.modal-open,
  &.modal-bg-open {
    color: #fff;
    .icon-phone {
      stroke: #fff; }
    a {
      color: #fff; }
    .header {
      &__location {
        cursor: pointer;
        @include hoverOpacity;
        .icon {
          fill: #fff; }
        .icon-small-arrow {
          border-top-color: #fff;
          @include trans;
          @include r(767) {
            display: none; } } }
      &__search-btn {
        & .icon {
          stroke: #fff; } }
      &__nav {
        a {
          &:before {
            background-color: #fff; } } } } }
  &.modal-open {
    &:before {
      opacity: 1;
      left: 0%;
      transition: .4s ease-in; }
    .header {
      &__logo {
        background-color: transparent;
        &:hover {
          transition-delay: 0s;
          background-color: $sapphire_h; }
        &:active {
          transition-delay: 0s;
          background-color: $sapphire_a; } } } } }

@include r(767) {
  .header {
    &-inner,
    &__search-btn,
    &__location {
      & .icon {
        font-size: 20px !important;
        height: 1em; } }
    &__left {
      width: 25%; }
    &__location {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center; }
    &__right {
      flex: 1 0 auto;
      > * {
        flex: 0 1 33.333%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0;
        height: 100%; } }
    &__language {
      font-size: 20px; } } }
