html, body {
  height: 100%; }

body {
  -webkit-tap-highlight-color: transparent;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  overflow-y: scroll;

  &.open-modal {
    position: fixed;
    left: 0;
    right: 0;
    top: 0; } }

.img-desktop {
  display: block;
  @include r(1023) {
    display: none !important; } }

.img-tablet {
  display: none !important;
  @include r(1023) {
    display: block !important; } }

.owl-carousel.in-dark,
.color-map {
  .owl-prev,
  .owl-next,
  .slider__next,
  .slider__prev {
    svg {
      fill: #fff; }
    &:hover {
      svg {
        fill: $orangeish_h; } }
    &:active {
      svg {
        fill: $orangeish_a; } }
    &.disabled {
      cursor: default !important;
      opacity: .3;
      svg {
        fill: #fff !important; } } }
  .slider__counter {
    color: #ffffff;
    &__line {
      background-color: #fff; } } }

.preloader {
  @include absoluteFill;
  position: fixed;
  background-color: #fff;
  z-index: 200;
  transition: .4s;
  &.hide {
    opacity: 0; }
  &__scale {
    position: absolute;
    left: 0;
    top: 0;
    height: 9px;
    background-color: #ff8e4b;
    transition: .4s linear; }

  &__val {
    @include h0;
    @include absoluteFill;
    display: flex;
    align-items: center;
    justify-content: center;
    &.next {
      transform: scale(1) !important;
      transition: .3s linear;
      &.hide {
        transition: 0s; } }
    &.hide {
      opacity: 0;
      transform: scale(.3);
      transition: .3s linear; } }
  &__copy {
    position: absolute;
    text-align: center;
    bottom: 50px;
    left: 0;
    right: 0;
    color: #989898; } }

body {
  @include proxima;
  @include fontStandart;
  line-height: 1.33; }
//background-color: #bfbfbf
.out {
  overflow-x: hidden;
  width: 100%;
  min-height: 100%;
  position: relative; }

input, textarea, button {
  @include proxima;
  @include fontStandart; }

.container {
  max-width: 1440px;
  padding: 0 80px;
  margin: 0 auto;
  @include r(1439) {
    padding: 0 40px; }
  @include r(1023) {
    padding: 0 30px; }
  @include r(767) {
    padding: 0 20px; }
  &-wrapper {
    padding: 0 80px;
    @include r(1439) {
      padding: 0 40px; }
    @include r(1023) {
      padding: 0 30px; }
    @include r(767) {
      padding: 0 20px; } } }

.hr {
  border-top: solid 1px #ececec;
  margin: 80px 0;
  @include r(1023) {
    margin: 70px 0; }
  @include r(767) {
    margin: 60px 0; } }

.quote-block {
  background-color: #FFF3ED;
  padding: 44px;
  display: flex;
  align-items: flex-start;
  max-width: 950px;
  margin: 0 auto 60px auto;
  @include r(767) {
    display: block;
    padding: 35px 20px;
    margin-left: -20px;
    margin-right: -20px; }
  &__icon {
    font-size: 160px;
    display: block;
    line-height: 0.85;
    margin-right: 38px;
    @include r(767) {
      font-size: 80px;
      height: 50px; } }
  &__descr {
    @include h4;
    @include pSemiIt;
    max-width: 620px;
    margin-bottom: 21px; } }

.list-dots {
  max-width: 620px;
  margin: 0 auto 50px auto;
  @include fontStandart;
  li {
    position: relative;
    padding-left: 15px;
    &:before {
      position: absolute;
      width: 4px;
      height: 4px;
      background-color: $squash;
      border-radius: 50%;
      content: '';
      top: 12px;
      left: 0; }
    margin-bottom: 7px; } }

.list-increment {
  max-width: 620px;
  margin: 0 auto 50px auto;
  li {
    position: relative;
    list-style: decimal;
    list-style-position: inside;
    color: #f7931d;
    span {
      color: #000000; }
    margin-bottom: 7px; } }

.btn-next {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 20px;
  @mixin tr {
    transition: .1s; }
  @include tr;
  .btn-next__icon {
    @include tr; }
  &__wrapper {
    display: flex; }
  &:last-child {
    margin-bottom: 0; }
  &.blue {
    color: $sapphire;
    .btn-next__icon {
      background-color: $sapphire; }
    &.disabled {
      color: $sapphire !important;
      .btn-next__icon {
        background-color: $sapphire !important; } }
    &:hover {
      .btn-next__icon {
        background-color: $sapphire_h; }
      color: $sapphire_h; }
    &:active {
      .btn-next__icon {
        background-color: $sapphire_a; }
      color: $sapphire_a; } }

  &.orange {
    color: $orangeish;
    .btn-next__icon {
      background-color: $orangeish; }
    &.disabled {
      color: $orangeish;
      .btn-next__icon {
        background-color: $orangeish !important; } }
    &:hover {
      .btn-next__icon {
        background-color: $orangeish_h; }
      color: $orangeish_h; }
    &:active {
      .btn-next__icon {
        background-color: $orangeish_a; }
      color: $orangeish_a; } }
  &__icon {
    width: 59px;
    height: 59px;
    display: flex;
    background: {
      image: url(data:image/svg+xml;charset=utf-8;base64,);
      position: center;
      repeat: no-repeat;
      size: 20px; }
    border-radius: 50%;
    margin-right: 20px; }
  &__text {
    @include h4; }
  &.disabled {
    opacity: .4;
    cursor: default; } }

.btn {
  @include h4;
  cursor: pointer;
  min-width: 284px;
  padding: 0 20px;
  line-height: 70px;
  height: 70px;
  border: 2px solid transparent;
  background-color: $orangeish;
  color: #ffffff;
  display: inline-block;
  text-align: center;
  transition: .1s linear;
  @include r(767) {
    min-width: 0;
    padding: 0 13px; }
  &.small {
    height: 60px;
    @include fontStandart;
    line-height: 60px;
    min-width: 0;
    padding: 0 35px; }
  &.border {
    border: solid 2px rgba(151, 151, 151, 0.2);
    background: transparent;
    color: #b7b7b7; }
  &[disabled="disabled"] {
    border: solid 2px rgba(151, 151, 151, 0.2);
    background: transparent;
    color: #b7b7b7; }
  &:hover {
    background-color: $orangeish_h;
    border-color: $orangeish_h;
    color: #ffffff; }
  &:active {
    background-color: $orangeish_a;
    border-color: $orangeish_a;
    color: #ffffff; }
  &.disabled {
    background-color: rgba(234, 234, 234, 0.24);
    border: solid 2px rgba(151, 151, 151, 0.2);
    color: rgba(183, 183, 183, 0.24);
    cursor: default; }
  &.unsuccess {
    border: solid 2px rgba(151, 151, 151, 0.2);
    background: transparent;
    color: #b7b7b7; } }

.map {
  &__mark {
    position: relative;
    width: 29px;
    height: 34px;
    transition: .1s linear;
    font-size: 0;
    cursor: pointer;
    transform: translate(-50%, -100%);
    svg {
      font-size: 34px;
      transition: .1s linear; }
    &:hover {
      svg {
        fill: #FF7F33; } }
    &.active {
      svg {
        fill: $sapphire; } }
    &:after {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      width: 100%;
      content: '';
      background: {
        image: url(data:image/svg+xml;charset=utf-8;base64,); } } } }

.checkbox,
.radio {
  cursor: pointer;
  * {
    cursor: pointer; }
  @mixin disabled {
    border: solid 1px #9f9f9f;
    background-color: #e8e8e8; }
  &__mark {
    width: 20px;
    height: 20px;
    border: solid 1px #9f9f9f;
    display: inline-block;
    position: relative;
    &:after {
      background-color: $orangeish;
      border-radius: 2px;
      width: 8px;
      height: 8px;
      content: '';
      opacity: 0;
      @include absoluteFill; }
    &:hover {
      border: solid 1px $orangeish; } }
  input {
    opacity: 0;
    height: 0;
    width: 0;
    &:checked {
      & ~ .checkbox__mark,
      & ~ .radio__mark {
        border: solid 1px $orangeish;
        &:after {
          opacity: 1; } } }
    &[disabled] {
      cursor: default;
      * {
        cursor: default; }
      & ~ .checkbox__mark,
      & ~ .radio__mark {
        @include disabled; } } }
  &.disabled {
    .checkbox__mark,
    .radio__mark {
      @include disabled; }
    * {
      cursor: default; } } }

.radio {
  &__mark {
    border-radius: 50%;
    &:after {
      border-radius: 50%; } } }

.input,
.textarea {
