    &.success {
      background-image: url(data:image/svg+xml;charset=utf-8;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCA0NC41IDU0Ij48cG9seWdvbiBmaWxsPSIjN2VkMzIxIiBwb2ludHM9IjE1LjE3IDU0IDEwLjkgNDguNzQgMCAzNC45MSA0LjA4IDI5LjcgMTQuNjUgNDIuMyAzOS4zMyAwIDQ0LjUgMy4yNyAxNS4xNyA1NCIvPjwvc3ZnPg==); }
    &.error {
      background-image: url(data:image/svg+xml;charset=utf-8;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCA1NCA1NCI+PHBvbHlnb24gcG9pbnRzPSI1NCAzLjYxIDUwLjM5IDAgMjcgMjMuMzkgMy42MSAwIDAgMy42MSAyMy4zOSAyNyAwIDUwLjM5IDMuNjEgNTQgMjcgMzAuNjEgNTAuMzkgNTQgNTQgNTAuMzkgMzAuNjEgMjcgNTQgMy42MSIgZmlsbD0iI2ZmNDg0OCIvPjwvc3ZnPg==);
      cursor: pointer; }
    &-message {
      font-size: 16px;
      padding: 28px 24px;
      position: absolute;
      width: 182px;
      background-color: #fff;
      box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.1);
      transform: translate(25px, -50%);
      opacity: 0;
      &:after {
        content: '';
        $b: 12px solid transparent;
        border-top: $b;
        border-bottom: $b;
        border-right: 10px solid #fff;
        position: absolute;
        width: 0;
        height: 0;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        transform: translate(-100%, 0); }
      &.arrow-bot {
        width: 280px;
        @include r(767) {
          left: 20px !important;
          right: 20px !important;
          width: auto; }
        &:after {
          right: 0;
          top: 0;
          bottom: auto;
          transform: translate(0, -100%);
          $b: 12px solid transparent;
          border: {
            bottom: 10px solid #fff;
            left: $b;
            right: $b;
            top: 0; } } } } }

  &:-webkit-autofill {
    color: #000000;
    &:hover,
    &:focus {
      color: #000000; } } }

.textarea {
  padding: 13px;
  resize: none;
  height: 169px; }

.expander {
  background-color: #FAFAFA;
  margin-bottom: 40px;
  @include r(767) {
    margin-left: -20px;
    margin-right: -20px;
    padding: 20px;
    margin-bottom: 20px; }
  &__title {
    padding-right: 175px;
    @include r(767) {
      padding-right: 40px; } }
  &__header {
    @include h3;
    @include futura;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 45px 50px;
    cursor: pointer;
    position: relative;
    @include r(1023) {
      padding: 35px 50px; }
    @include r(767) {
      padding: 16px 0; }
    .icon-arrow {
      font-size: 16px;
      transition: .4s;
      position: absolute;
      right: 50px;
      top: 50%;
      transform: translate(0, -50%);
      @include r(767) {
        top: 33px;
        right: 20px; } } }

  &__content {
    padding: 0 50px 45px 50px;
    @include r(767) {
      padding: 0;
      padding-bottom: 20px; }
    &.p0 {
      padding-top: 0 !important; }
    //transition: .3s
    &-wrapper {
      overflow: hidden;
      height: 0;
      .expander__content {
        opacity: 0; } } }
  &.big {
    background-color: transparent;
    .expander {
      &__header,
      &__content {
        padding-left: 0;
        padding-right: 0;
        border-bottom: solid 1px #ececec; }
      &__header {
        @include h2;
        @include r(1023) {
          padding: 35px 0; }
        .icon-arrow {
          right: 0;
          @include r(767) {
            top: 58px; } } }
      &__content {
        padding-top: 52px; } }
    @include r(767) {
      padding-top: 0;
      padding-bottom: 0;
      .expander__header {
        padding-top: 20px;
        padding-bottom: 20px;
        .icon-arrow {
          top: 44px; } } } }

  &.active {
    height: auto;
    .expander {
      &__header {
        & .icon-arrow {
          transform: rotate(180deg); } }
      &__content {
        opacity: 1;
        &-wrapper {
          height: auto; } } } } }

.file-upload {
  position: relative;
  &__input {
    font-size: 0;
    height: 0;
    width: 0;
    opacity: 0;
    padding: 0;
    position: absolute; }
  &__header {
    color: $sapphire;
    display: flex;
    align-items: center;
    cursor: pointer;
    .icon {
      margin-right: 15px; } }
  &__files {
    margin-top: 15px;
    padding-left: 32px;
    display: block; }
  &__file {
    display: flex;
    align-items: center;
    &__remove {
      font-size: 0;
      line-height: 0;
      cursor: pointer;
      background: {
        image: url(data:image/svg+xml;charset=utf-8;base64,); }
      width: 14px;
      height: 14px; }

    &__percentage {
      color: $orangeish; }
    &__title {
      margin: 0 15px; }
    &__right {
      display: flex;
      align-items: center;
      flex: auto;
      justify-content: space-between; }

    .icon-close {
      font-size: 14px;
      fill: #7F7F7F; } } }

.modal-prompt {
  padding: 38px 86px 38px 30px;
  background-color: #fff;
  box-shadow: 0 14px 39px 0 rgba(0, 0, 0, 0.1);
  display: inline-block;
  position: absolute;
  opacity: 0;
  transform: translate(0, -100%);
  transition: .3s ease opacity;
  @include r(767) {
    padding: 20px 40px 20px 20px; }
  &.active {
    opacity: 1; }
  &__arrow {
    content: '';
    $b: 18px solid transparent;
    border-bottom: $b;
    border-left: 18px solid #fff;
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translate(0, 100%); }

  &__title {
    margin-bottom: 16px;
    @include h4; }
  .close {
    background-image: url(data:image/svg+xml;charset=utf-8;base64,);
    width: 16px;
    height: 16px;
    cursor: pointer;
    position: absolute;
    top: 19px;
    right: 19px;
    @include r(767) {
      top: 12px;
      right: 12px; } } }

