.vacancies {
  display: flex;
  @include r(767) {
    &-section {
      padding-bottom: 0; } }
  @include r(1023) {
    display: block; }
  .expander__title {
    @include h3;
    @include r(1023) {
      padding-right: 40px; } }
  &__item {
    &__header {
      display: flex;
      @include fontStandart;
      width: 100%;
      justify-content: space-between;
      margin-top: 42px;
      @include proxima;
      font-weight: normal;
      align-items: center;
      @include r(767) {
        margin-top: 17px;
        display: block; } }
    &__salary {
      @include h4;
      @include r(767) {
        margin-top: 6px; } }
    &__list {
      margin-bottom: 28px;
      @include r(767) {
        margin-top: 10px; }
      &__title {
        @include h4;
        margin-bottom: 7px; } } }
  &__left {
    flex: auto; }
  &__right {
    flex: 1 1 324px;
    margin-left: 60px;
    .life-company {
      display: none;
      @include r(1023) {
        display: flex;
        margin-left: -30px;
        margin-right: -30px; }
      @include r(767) {
        margin-left: -20px;
        margin-right: -20px; } }
    @include r(1023) {
      margin-left: 0;
      .banner-life-company {
        display: none; } }
    @include r(767) {
      margin-top: 50px; } } }

