.gallery {
  .customSlider-wrapper {
    @include absoluteFill;
    position: fixed;
    z-index: 100;
    opacity: 0;
    visibility: hidden;

    .owl-carousel,
    .owl-stage-outer,
    .owl-stage,
    .owl-item {
      height: 100%;
    }

    .owl-nav {
      right: 80px;
    }

    &.active {
      opacity: 1;
      visibility: visible;
    }

    .owl-carousel {
      position: static !important;
    }
  }

  .customSlider-item {
    height: 100%;
    padding: 0;
  }

  .customSlider-more {
    color: #000;
  }

  .closeGallery {
    position: absolute;
    top: 120px;
    right: 80px;
    background-color: $orangeish;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 54px;
    height: 54px;
    border-radius: 99px;
    cursor: pointer;

    &:hover {
      background-color: $orangeish_h;
    }

    &:active {
      background-color: $orangeish_a;
    }

    .icon-close {
      font-size: 16px;
      fill: #fff;
    }
  }

  @include small-viewport {
    .closeGallery,
    .customSlider-wrapper .owl-nav {
      right: 40px;
    }
  }

  @include tablet-viewport {
    .closeGallery {
      top: 110px;
    }

    .closeGallery,
    .customSlider-wrapper .owl-nav {
      right: 30px;
    }
  }
}
