.roofing {
  &__hero {
    margin-bottom: 0;
    height: auto;
    min-height: 100vh;

    .site-nav__next {
      display: none; }

    &__info {
      padding-top: 230px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      min-height: 100vh;
      @include r(767) {
        padding-top: 170px; }
      &-inner {
        padding: 0 80px;
        display: flex;
        @include r(1023) {
          flex-direction: column;
          padding-left: 200px; }
        @include r(767) {
          padding-left: 20px; } }
      .page__hero__title {
        position: static; } }
    &__left {
      flex: 1 0 320px;
      max-width: 320px;
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      sup {
        margin-left: -.4em;
        vertical-align: top;
        font-size: .6em; }
      @include r(1023) {
        order: 2;
        flex: 0 1;
        margin-top: 57px; }
      @include h4;
      .btn {
        display: inline-block;
        margin-top: 25px;
        @include r(767) {
          margin-top: 18px; } } }
    &__left,
    &__right {
      position: static; }
    &__img {
      font-size: 0;
      width: 100%;
      margin-top: 84px;
      //transform: translate(0, 50%)
      @include r(767) {
        margin-top: 60px; }
      img {
        width: 100%;
        height: auto; }
      @include r(767) {
        margin-left: -135px;
        margin-right: -135px;
        width: auto; } } } }
.gost {
  display: flex;
  justify-content: space-between;
  @include r(767) {
    display: block; }
  .col {
    margin-right: 70px;
    &:last-child {
      margin-right: 0; } }
  &__left {
    flex: 1 0 200px;
    width: 100%;
    &.col {
      margin-right: 9.5%; }
    @include r(767) {
      margin-bottom: 20px; } }
  &__title {
    @include h3;
    @include r(1023) {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } }
    @include r(767) {
      margin-right: 0 !important; } }
  @include r(1023) {
    .gost__info {
      display: block; } } }
.roofing-profil {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include r(1023) {
    display: block; }
  .col {
    flex: 0 1 50%;
    margin: 0 50px;
    &:first-child {
      margin-left: 0; }
    &:last-child {
      margin-right: 0; }
    @include r(1023) {
      margin: 0 0 50px 0;
      &:last-child {
        margin-bottom: 0; } }
    @include r(767) {
      padding: 20px;
      margin: 0 -20px;
      background-image: none; } }
  &__descr {
    @include h4;
    margin-top: 62px;
    @include r(767) {
      margin-top: 30px; } }
  &__panel {
    padding: 60px;
    background-image: linear-gradient(203deg, #f7f7f7, #e8e8e8);
    @include r(1023) {
      padding-left: 30px;
      padding-right: 30px; }
    img {
      margin-left: -60px;
      max-width: 100%;
      @include r(1023) {
        margin-left: -30px; }
      @include r(767) {
        margin-left: -20px; } }
    &__title {
      @include h3;
      margin-top: 40px;
      @include r(767) {
        margin-top: 18px; } } } }
