@include rmin(767) {
  @include r(1023) {
    @include rh(1024) {
      .main__home {
        .main__bot {
          &__right,
          &__left {
            padding: 40px; } } } }
    @include rh(800) {
      $fzNav: 20px;
      $top: 90;
      .main__screen {
        .main__hero {
          min-height: 33.33vh; }
        .owl {
          &-next,
          &-prev {
            width: 34px;
            svg {
              height: 13px; } } }
        .site-nav {
          &__back {
            top: $top + px !important;
            font-size: $fzNav; }
          &__next {
            font-size: $fzNav; } }
        .slider {
          &__item {
            &-pic {
              padding-bottom: 60px; } }
          &__dots {
            top: $top + 20 + px; }
          &__dot {
            margin-bottom: 7px;
            font-size: 15px;
            &.active {
              &:before {
                top: 6px; } } } }
        .slider__counter {
          top: $top + px !important; }
        h1 {
          font-size: 32px; }
        h2,
        .main__objects__numbers__title {
          font-size: 24px; }
        .icon-arrow {
          font-size: 16px; }
        .h4 {
          font-size: 16px; }
        .btn-next__wrapper {
          transform: scale(.7);
          transform-origin: bottom left; }
        .service__title {
          margin-bottom: 10px !important; } }
      .main__objects__title {
        margin-bottom: 10px; }
      .service__descr,
      .main__objects__info {
        top: 270px;
        font-size: 18px; }
      .main__objects__descr {
        font-size: 18px; }
      .main__objects__numbers {
        margin-bottom: 0px !important; }
      .main__objects__review,
      .slider__all {
        font-size: $fzNav;
 }        //margin-top: 10px
      .slider__counter {
        font-size: $fzNav;
        &__line {
          width: 24px;
          height: 2px;
          margin: 0 10px; } }
      .main__catalog {
        .slider__dots {
          top: -12px; }
        .slider__item {
          padding-top: 130px !important; } }
      .main__home {
        font-size: 16px;
        h1 {
          font-size: 60px; }
        .main {
          &__bot {
            &__left {
              right: 50%;
              bottom: 0; }
            &__right {
              left: 50%;
              top: 0; }
            &__left,
            &__right {
              padding: 20px; } }
          &__numbers {
            display: block; }
          &__number {
            &__title {
              font-size: 30px; } } } }
      .main__news {
        .slider__all {
          bottom: 40px !important; } } } } }
