.life-company {
  padding: 0 90px 0 90px;
  background-color: $very-light-pink;
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  position: relative;
  @include r(767) {
    flex-direction: column;
    padding: 20px;
    &__sub {
      display: none; } }
  &__info {
    width: 100%;
    flex: 1 1 50%;
    padding: 90px 0 135px 0;
    @include r(1023) {
      padding: 50px 0 60px 0;
      .life-company {
        &__date {
          display: none; }
        &__descr {
          display: none; } } }
    @include r(767) {
      padding: 10px 0; }

    &-inner {
      max-width: 510px; } }
  &__title {
    margin-bottom: 75px;
    @include r(1439) {
      margin-bottom: 40px; }
    @include r(1023) {
      margin-bottom: 25px; }
    @include r(767) {
      margin-bottom: 0; } }
  &__date {
    margin-bottom: 15px;
    display: block;
    @include h4; }
  &__descr {
    @include h3;
    margin-bottom: 30px; }
  &__pic {
    flex: 1 1 50%;
    width: 50%;
    margin-top: -135px;
    font-size: 0;
    margin-right: 78px;
    text-align: center;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    justify-content: flex-end;
    img {
      max-height: 100%;
      max-width: 100%; }
    @include r(1023) {
      margin-right: 42px;
      img {
        max-height: 150%;
        margin-bottom: -50px; } }
    @include r(767) {
      order: 2;
      width: 100%;
      position: static;
      margin-top: 32px;
      margin-bottom: -20px;
      img {
        margin-bottom: -80px; } } } }


.pdf-files {
  display: flex;
  flex-wrap: wrap;
  @include r(767) {
    display: block; } }

.pdf-file {
  flex: 1 1 25%;
  @include r(1023) {
    flex: 0 1 50%;
    margin-bottom: 44px;
    padding-right: 30px; }
  @include r(767) {
    display: block; }
  &:hover {
    .pdf-file__title {
      color: $orangeish_h; } }
  &:active {
    .pdf-file__title {
      color: $orangeish_a; } }
  &__title {
    padding-right: 40px;
    margin-top: 22px;
    margin-bottom: 10px;
    color: #000000;
    @include trans1;
    @include r(767) {
      margin-top: 6px;
      margin-bottom: 8px; } }

  &__info {
    color: #a8a8a8; } }

.hero-video {
  height: 100%;
  overflow: hidden;
  video {
    @include absoluteFill;
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.video {
  position: relative;
  font-size: 0;
  @include r(767) {
    &__title {
      margin-bottom: 20px; } }
  &__blackout {
    background-color: rgba(#000, .2);
    @include absoluteFill; }
  video {
    width: 100%; }
  &.play {
    .video__play {
      transform: scale(.1);
      transition: .3s cubic-bezier(0.6, -0.47, 0.25, 1); }
    .video__cover {
      opacity: 0;
      visibility: hidden; } }
  &__cover {
    @include absoluteFill;
    @include bgcc;
    @include trans; }
  &__info {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 60px;
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    @include h3;
    @include r(1023) {
      padding: 30px; }
    @include r(767) {
      justify-content: center;
      padding: 15px; } }
  .video__play {
    @include absoluteFill;
    margin: auto;
    @include trans1;
    .video__dur {
      @include h4;
      position: absolute;
      bottom: -15px;
      color: #ffffff;
      transform: translate(0, 100%);
      white-space: nowrap;
      @include r(767) {
        bottom: -8px; } } }
  &__play {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-color: $orangeish_a;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    @include r(1439) {
      width: 100px;
      height: 100px; }
    @include r(767) {
      width: 50px;
      height: 50px; }
    &:before {
      $b: 19px solid transparent;
      border: {
        top: $b;
        bottom: $b; }
      border-left: 26px solid #fff;
      margin-left: 8px;
      content: '';
      @include r(1439) {
        $b: 16px solid transparent;
        border: {
          left: 22px solid #fff;
          top: $b;
          bottom: $b; } }
      @include r(767) {
        $b: 8px solid transparent;
        margin-left: 2px;
        border: {
          top: $b;
          bottom: $b;
          left-width: 11px; } } }
    &:hover {
      background-color: $orangeish_h; }
    &:active {
      background-color: $orangeish_a; } } }

.banner-video {
  height: 246px;
  background-image: url(../img/banner-video.jpg);
  @include bgcc;
  margin-bottom: 40px;
  color: #ffffff;
  @include h3;
  padding: 78px 50px;
  position: relative;
  display: block;
  @include r(767) {
    padding: 50px 30px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: -20px;
    margin-right: -20px; }
  .video__play {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 16.6%;
    @include trans1;
    @include r(767) {
      position: static;
      margin-top: 28px; } } }

.banner-life-company {
  position: relative;
  &__img {
    font-size: 0;
    position: relative;
    margin-bottom: 20px;
    img {
      margin-top: -66px;
      position: relative; }
    &:before {
      content: '';
      @include absoluteFill;
      background-color: $very-light-pink; } }
  .link-next {
    margin-top: 14px; } }

.parallax-circles {
  padding: 185px 0;
  position: relative;
  z-index: 2;
  transform-style: preserve-3d;
  @include r(1439) {
    padding: 140px 0; }
  @include r(1023) {
    padding: 80px 0; }
  @include r(767) {
    padding: 40px 0; }
  .flex {
    justify-content: space-between;
    position: relative;
    @include r(1023) {
      display: block; }
    .parallax-circles {
      &__title {
        margin-right: 60px;
        @include r(767) {
          margin-right: 0; } }
      &__descr {
        flex: 1 0 320px;
        @include r(1023) {
          margin-top: 30px; } } } }
  &__title {
    @include h3;
    &.big {
      @include h2; } }
  .circle {
    width: 320px;
    height: 320px;
    background: {
      color: #ccc;
      size: cover;
      position: center; }
    border-radius: 50%;
    position: absolute;
    pointer-events: none;
    @include r(1439) {
      width: 280px;
      height: 280px; }
    @include r(1023) {
      width: 180px;
      height: 180px; }
    @include r(767) {
      width: 100px;
      height: 100px; } }
  &__items {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    @include r(1023) {
      display: block;
      margin-left: 170px; }
    @include r(767) {
      margin-left: 70px; } }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include r(1023) {
      margin-bottom: 50px;
      &:last-child {
        margin-bottom: 0; } }
    &__title {
      margin-bottom: 42px;
      @include r(767) {
        margin-bottom: 22px; } }
    &__descr {
      max-width: 280px; } } }


.color-map {
  position: relative;
  color: #ffffff;
  $height: 50;
  $height1: #{$height / 2}vw;
  $height2: #{$height}vw;
  .slider {
    &__nav {
      display: flex;
      position: static;
      height: 0;
      @include r(767) {
        height: 8px; } }
    &__prev,
    &__next {
      position: absolute;
      z-index: 4;
      top: 0;
      bottom: 0;
      left: auto;
      margin: auto;
      padding: 20px 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      @include cRight;
      cursor: pointer;
      @include trans1;
      .icon {
        fill: #ffffff !important; }
      &:hover {
        opacity: .6; }
      @include r(1280) {
        top: 85px;
        bottom: auto; }
      @include r(767) {
        top: auto;
        bottom: 225px;
        right: 20px; } }
    &__prev {
      right: 160px;
      @include r(1439) {
        right: 120px; }
      @include r(1023) {
        right: 100px; }
      @include r(767) {
        right: 80px; } } }

  &__title {
    position: absolute;
    top: 70px;
    @include cLeft;
    z-index: 4;
    @include r(767) {
      top: 38px;
      left: 20px; } }
  &__description {
    position: absolute;
    z-index: 4;
    max-width: 20%;
    &:nth-child(1) {
      right: 50%;
      bottom: 40px;
      margin-right: 48px;
      @include r(1280) {
        position: absolute;
        bottom: 162px;
        right: 112px;
        max-width: 272px;
        margin-right: 0; }
      @include r(767) {
        left: 20px;
        right: 20px;
        bottom: 120px;
        max-width: none; } }
    &:nth-child(2) {
      top: 76px;
      right: 40px;
      @include r(1280) {
        display: none; } } }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: repeat(2, $height1);
    @include r(1280) {
      display: flex;
      flex-wrap: wrap; } }
  &__item {
    height: $height1;
    background-color: #ccc;
    position: relative;
    z-index: 2;
    cursor: pointer;
    transition: .6s ease;
    @include r(1280) {
      flex: 0 1 25%;
      min-height: 0;
      padding: 46px 18px 10px 18px;
      height: auto;
      &:hover {
        transform: rotate(0); } }
    @include r(767) {
      height: 60px;
      flex: 0 1 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      &:nth-child(4),
      &:nth-child(5) {
        display: none; } }
    &__texture {
      @include absoluteFill;
      transition: .6s ease;
      z-index: -1;
      background: {
        position: center;
        size: cover; } }
    &:hover {
      transform: scale(1.025);
      position: relative;
      z-index: 3; }
    @include bgcc;
    &:nth-child(2) {
      grid-row: 1;
      grid-column: 3; }
    &:nth-child(3) {
      grid-row: 2;
      grid-column: 3; }
    &:nth-child(4) {
      grid-column: 4;
      grid-row: 1;
      height: $height2;
      @include r(1280) {
        height: auto; } }
    &:last-child {
      display: none;
      @include r(1280) {
        display: block; } }


    &__title {
      position: absolute;
      left: 30px;
      bottom: 35px;
      @include h4;
      @include r(1280) {
        position: static; } }
    &.main {
      height: $height2;
      grid-column: 1 / 3;
      grid-row: 1;
      display: flex;
      align-items: center;
      cursor: default;
      @include r(1280) {
        height: auto;
        padding: 250px 0; }
      @include r(767) {
        padding: 130px 0 240px; }

      transform: scale(1) rotate(0) !important;
      position: static !important;
      z-index: 1 !important;
      @include r(1280) {
        flex: 0 1 100%; }

      .color-map__item {
        &__title {
          font-size: 46px;
          left: 75px;
          bottom: 30px;
          @include r(1280) {
            position: absolute;
            bottom: 62px;
            left: 40px; }
          @include r(767) {
            font-size: 28px;
            left: 20px;
            bottom: 165px; } } } }
    &__img {
      padding-right: 12%;
      @include absoluteFill;
      display: flex;
      align-items: center;
      justify-content: left;
      opacity: 0;
      transition: .6s ease;
      &.active {
        @include op1tt00; }
      img {
        max-width: 100%; }
      @include r(1280) {
        width: 100%;
        flex: 1 0 100%;
        position: relative;
        justify-content: flex-start;
        z-index: 3;
        display: none;
        &.active {
          display: flex; } } } } }

.map {
  position: relative;
  &__header {
    display: flex;
    justify-content: space-between;
    @include r(1023) {
      .map__header {
        &__left,
        &__right {
          flex: 0 1 50%; } } }
    @include r(767) {
      flex-direction: column;
      margin-bottom: 20px;
      &__left {
        order: 2;
        margin-top: 40px; } }
    .link-next {
      margin-top: 35px; }
    &__descr {
      max-width: 528px;
      @include h4;
      margin-top: 35px; } }
  .icon {
    width: 100%;
    height: auto; }
  &__mark {
    position: absolute;
    @include r(767) {
      transform: scale(.7) translate(-50%,-100%);
      transform-origin: 0 0; }
    .icon {
      height: 34px; } }
  &__info {
    position: relative;
    left: 0;
    bottom: 0;
    &__title {
      @include h3;
      margin-bottom: 20px; } } }
@include r(767) {
  .container {
    .pic-cover {
      margin-left: -20px;
      margin-right: -20px; } } }
.pic-cover {
  position: relative;
  font-size: 0;
  img {
    width: 100%;
    max-width: 100%; }
  //max-height: 100vh
  //object-fit: cover
  &__descr {
    min-width: 245px; }
  &__info {
    @include fontStandart;
    color: #ffffff;
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 40px;
    min-height: 316px;
    z-index: 3;
    @include r(767) {
      position: relative;
      min-height: 0;
      padding: 34px 24px; }
    &__inner {
      position: relative; }
    &__bg {
      background-color: $orangeish;
      @include absoluteFill; }
    &.big {
      padding: 50px;
      padding-right: 189px;
      min-height: 388px;
      @include r(1439) {
        min-height: 227px;
        padding: 45px 100px 45px 50px; }
      @include r(1023) {
        min-height: 180px;
        padding: 40px 20px 30px 35px; }
      @include r(767) {
        padding: 30px 20px; } } }
  &__title {
    margin-bottom: 20px;
    @include r(1023) {
