.graphic-growth {
  display: flex;
  height: 50vw;
  min-height: 640px;
  align-items: flex-end;
  padding-top: 70px;
  transform-style: preserve-3d;
  @include r(1439) {
    min-height: 520px; }
  @include r(1023) {
    min-height: 440px; }
  @include r(767) {
    display: block;
    margin-left: -20px;
    margin-right: -20px;
    height: auto;
    padding-top: 120px; }

  &-wrapper {
    position: relative; }
  &__title {
    position: absolute;
    top: 0;
    left: 0; }
  &__col {
    position: relative;
    padding: 35px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex: auto;
    @include r(767) {
      height: 130px !important;
      position: static;
      display: flex;
      justify-content: center;
      padding: 15px; }
    &-wrapper {
      flex: 0 1 25%;
      height: 100%;
      display: flex;
      align-items: flex-end;
      @include r(767) {
        position: relative;
        height: 130px;
        display: block; }
      &:nth-child(1) {
        .graphic-growth__col {
          background-color: $very-light-pink; } }
      &:nth-child(2) {
        .graphic-growth__col {
          background-color: #ffdac3; } }
      &:nth-child(3) {
        .graphic-growth__col {
          background-color: #ffc098; } }
      &:nth-child(4) {
        .graphic-growth__col {
          background-color: $orangeish;
          color: #ffffff;
          @include r(767) {
            .graphic-growth__col__header {
              color: #ffffff;
              top: 10px;
              right: 22px; } } } } }
    @include r(1023) {
      padding: 20px; }
    @include r(767) {
      height: 130px !important; }
    &__header {
      position: absolute;
      left: 0;
      top: -53px;
      color: #000000;
      @include r(767) {
        left: auto;
        top: 0;
        right: 20px; } }
    &__percent {
      @include h3;
      @include r(767) {
        display: flex;
        font-size: 20px;
        .big-title {
          font-size: 60px;
          margin-right: 22px; } } } } }

.in-about {
  padding-bottom: 0 !important;
  .life-company {
    margin-top: 0; } }

.page__hero__play {
  display: inline-block;
  color: $orangeish;
  align-items: center;
  margin-top: 55px;
  cursor: pointer;
  @include r(1023) {
    margin-top: 20px; }
  &.in-dark {
    color: #ffffff; }
  @include trans1;
  &:hover {
    color: $orangeish_h;
    .video__play {
      background-color: $orangeish_h; } }

  &:active {
    color: $orangeish_a;
    .video__play {
      background-color: $orangeish_a; } }

  @include h4;
  .video__play {
    @include trans1;
    width: 55px;
    height: 55px;
    position: static;
    margin: 0 20px 0 0;
    vertical-align: middle;
    text-align: center;
    display: inline-block;
    position: relative;
    @include r(767) {
      width: 50px;
      height: 50px;
      margin-right: 15px; }

    &:before {
      @include absoluteFill;
      height: 0;
      width: 0;
      left: 4px;
      margin: auto;
      border: {
        top-width: 8px;
        bottom-width: 8px;
        left-width: 12px; } } } }

.parallax-transfusion {
  &-imgs {
    @include r(767) {
      @include absoluteFill; }
    img {
      @include absoluteFill;
      opacity: 0;
      //transition: 1s ease
      @include r(767) {
        bottom: auto; }
      &.show {
        opacity: 1;
        z-index: 2; } } } }
@include r(767) {
