.main {
  &__home {
    background: {
      size: cover;
      position: center; }
    .big-title {
      transform: translate(0, 0);
      font-size: 15vw; } }
  &__screens {
    height: 100%;
    width: 100%;
    @include absoluteFill;
    position: fixed;
    @include r(767) {
      position: static; } }
  &__screen {
    height: 100vh;
    width: 100%;
    @include absoluteFill;
    @include mPT;
    flex-direction: column;
    background-color: #fff;
    display: flex;
    .owl-stage {
      font-size: 0; }
    @include r(767) {
      position: relative;
      display: block !important;
      .slider__dots {
        display: none; }
      .site-nav {
        display: none; } }
    .video {
      font-size: 0;
      @include absoluteFill;
      @include r(767) {
        bottom: 50%; }
      video {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
    &.static {
      position: relative;
      .owl-carousel {} }
    .owl {
      &-carousel {
        @include absoluteFill; }
      &-nav {
        display: flex; }
      &-prev,
      &-next {
        top: 0;
        bottom: 0;
        width: 54px; }
      &-prev {
        @include cLeft; }
      &-next {
        left: auto;
        right: 80px;
        @include r(1023) {
          right: 30px; } } }
    .slider__nav {
      @include headerLeft; }
    &.in-dark {
      color: #fff;
      .owl-prev,
      .owl-next {
        &.disabled {
          svg {
            fill: #fff; } } } }
    .slider {
      .owl {
        &-carousel,
        &-stage-outer,
        &-stage,
        &-item {
          height: 100%; }
        &-stage-outer {
          overflow: visible; }
        &-item {
          opacity: 0;
          transition-duration: .4s;
          transition-timing-function: cubic-bezier(0, 0, 0.04, 1);
          transition-property: opacity, transform;
          img {
            transform: translate(100px, 0);
            transition-duration: .4s;
            transition-timing-function: cubic-bezier(0, 0, 0.04, 1);
            transition-property: opacity, transform; }
          &.active {
            opacity: 1;
            img {
              transform: translate(0, 0) !important; } } } }
      &__item {
        height: 100%;
        background: {
          position: center;
          size: cover;
          repeat: no-repeat; } } }
    &.main__catalog .slider {
      position: absolute;
      @include hTop;
      right: 80px;
      @include cLeft;
      @include cBot;

      .owl-nav {
        top: -50px;
        @include r(1439) {
          top: -90px; }
        @include r(1023) {
          top: -80px; } }

      img {
        margin-left: 650px;
        max-height: 100%;
        width: 70%;
        height: auto;
        object-fit: contain;

        &.pic-rolls {
          margin-left: 400px;
          width: 110%;
          transform: translate(0, -100px);
          max-height: none; }
        &.pic-fasson {
          margin-top: -100px;
          margin-left: -100px;
          width: calc(100% + 200px);
          min-width: 1440px;
          transform: translate(0, -100px);
          max-height: none;
          @include r(1023) {
            padding-bottom: 100px; }
          @include r(767) {
            padding-bottom: 0; } } }
      .owl {
        &-prev {
          left: 0; }
        &-next {
          left: 82px;
          right: auto; } }
      .slider__counter {
        top: auto;
        right: 0;
        bottom: 0; } }

    .container-wrapper {
      flex: auto;
      display: flex;
      flex-direction: column; }
    &:nth-child(1) {
      padding-top: 0; }
    &:nth-child(2) {
      display: none; } }
  &__hero {
    height: 64vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
    color: #fff;
    @include bgcc;
    @include r(767) {
      height: 65vh; } }
  &__bot {
    height: 36vh;
    display: flex;
    position: relative;
    padding-left: 80px;
    @include r(767) {
      //min-height: 50vh
      min-height: 0;
      height: 210px; }
    &__info {
      width: calc(100% / 1.5); }
    &__left,
    &__right {
      padding: 60px 60px 73px 60px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @include r(767) {
        .h4 {
          font-size: 18px; } } }
    &__left {
      background-color: #f5f5f5;
      left: 0;
      right: 50%;
      @include r(1023) {
        bottom: 50%;
        .h4 {
          max-width: 567px; } }
      @include r(767) {
        bottom: 0;
        padding: 15px 20px; } }
    &__right {
      background-color: #fff;
      @include r(1023) {
        top: 50%; }
      @include r(767) {
        display: none; } } }
  &__numbers {
    display: flex;
    @include r(1439) {
      display: block; }
    @include r(1023) {
      display: flex; } }
  //justify-content: space-between
  &__number {
    margin-right: 87px;
    @include r(1439) {
      margin-bottom: 20px; }
    &:last-child {
      margin-right: 0; }
    &__title {
      @include h3;
      @include futura;
      margin-bottom: 5px; }
    &__descr {
      color: #aeaeae; } }

  &__catalog {
    background-color: #eaeaea;
    overflow: hidden;
    .owl-nav {
      left: 0; }
    .slider__dots {
      left: 320px;
      @include r(1439) {
        left: 276px; }
      @include r(1023) {
        left: 170px;
        margin-top: -5px; } } } }

.service {
  &__info {
    color: #000;
    position: absolute;
    @include headerLeft;
    bottom: 0;
    right: 0;
    min-height: 450px;
    padding: 50px 74px;
    background-color: #fff;
    @include small-viewport {
      padding: 40px 60px;
      min-height: 0; }
    @include tablet-viewport {
      min-height: 0;
      padding: 40px 30px;
      min-height: 0; }
    @include mobile-viewport {
      min-height: 0;
      padding: 30px 20px;
      left: 0; }

    .btn-next__wrapper {
      margin-top: 42px; } }
  &__title {
    font-size: 72px;
    font-weight: bold;
    margin-bottom: 42px;
    max-width: 824px;
    @include small-viewport {
      font-size: 56px;
      margin-bottom: 30px; }
    @include tablet-viewport {
      font-size: 56px;
      margin-bottom: 20px; }
    @include mobile-viewport {
      font-size: 36px;
      margin-bottom: 22px; } }
  &__descr {
    @include h4;
    max-width: 758px;
    @include r(767) {
      font-size: 16px !important; } } }

.main__services {
  .owl {
    &-prev,
    &-next {
      top: 240px;
      bottom: auto; } } }

.main__objects {
  @include rmin(1280) {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

  .slider__counter {
    display: none;

    @include mobile-viewport {
      display: flex; } }

  @include r(767) {
    min-height: 0; }

  .main__screen__header {
    @include mobile-viewport {
      position: absolute;
      left: 20px;
      top: 40px;
      bottom: auto; } }

  .slider__item__bot {
    align-items: center;

    @include tablet-viewport {
      align-items: flex-end; }

    @include mobile-viewport {
      padding: 40px 20px;
      height: auto;

      .slider__item__bot__left {
        flex: 1 1; } }

    &__left {
      display: flex;
      align-items: center;

      @include tablet-viewport {
        display: block; } }

    .main__objects__review__wrapper {
      width: 320px;

      @include small-viewport {
        width: 236px; }

      @include tablet-viewport {
        width: auto;
        margin-bottom: 30px; }

      @include mobile-viewport {
        display: none; } } }

  .slider__all-wrapper {
    @include mobile-viewport {
      margin-top: 40px;
      padding-left: 60px; } }

  a.btn-next {
    color: #fff !important; }

  .btn-next__icon {
    @include mobile-viewport {
      margin-right: 10px; } }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: static;
    padding-left: 320px;
    max-width: 524px;
    height: 100%;
    flex: 0 1 auto;
    box-sizing: content-box;
    max-height: 600px;
    @include fontStandart;

    @include small-viewport {
      padding-left: 236px; }

    @include tablet-viewport {
      padding-left: 0; }

    @include mobile-viewport {
      display: block;
      height: auto; }

    &:after {
      content: '';
      height: 30px; }

    &-wrapper {
      @include headerLeft;
      z-index: 2;
      top: 120px;
      padding-top: 150px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      @include tablet-viewport {
        padding-top: 0; }

      @include mobile-viewport {
        position: static;
        padding-top: 32px;
        width: 100%;
        display: block; } }

    @include rmin(1280) {
      min-height: 480px;
      top: auto; }
    @include r(767) {
      position: static;
      padding: 20px;
      padding-top: 140px; } }
  &__title {
    font-weight: bold;
    margin-bottom: 20px;
    @include r(1439) {
      margin-bottom: 15px; } }
  &__descr {
    @include h4; }
  &__header {
    @include r(767) {
      margin-right: 0;
      margin-bottom: 0; } }
  &__numbers {
    display: flex;
    @include r(767) {
      margin-bottom: 0; }
    &__title {
      @include h3;
      @include futura;
      margin-bottom: 15px;
      @include r(1439) {
        margin-bottom: 10px; } } }
  &__number {
    margin-right: 63px;

    @include mobile-viewport {
      margin-right: 24px; }

    &:last-child {
      margin-right: 0; } }
  .slider__all {
    position: static; } }

.main__object {
  .owl-carousel {
    overflow: hidden; }
  &__slider {
    //z-index: -1
    &__wrapper {
      z-index: 1;
      .slider__item {
        &:before {
          content: '';
          background: rgba(#000, .5);
          @include absoluteFill; } } }
    .owl {
      //&-carousel
      &-item,
      &-stage,
      &-stage-outer {
        z-index: -1; }
      &-stage {
        pointer-events: none; }

      &-nav {
        display: none; } }
    &__item {
      @include absoluteFill;
      @include bgcc;
      z-index: -1;
      &:before {
        content: '';
        background: rgba(#000, .5);
        @include absoluteFill; } }

    .owl {
      &-dots {
        position: absolute;
        @include cLeft;
        bottom: 260px;
        display: flex;
        align-items: center;
        z-index: 2;
        @include r(1023) {
          left: 200px;
          bottom: 140px; }
        @include r(767) {
          display: none; } } } } }

.main__review {
  position: fixed;
  display: none;
  top: 80px;
  @include cBot;
  @include headerLeft;
  background-color: #fff;
  z-index: 4;
  color: #000;
  padding: 100px 80px 80px 60px;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
  transform: translate(100%, 0);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: transparent;
  @include r(1439) {
    top: 0;
    left: 0px;
    bottom: 0;
    bottom: 0;
    z-index: 100; }
  @include r(1023) {
    left: 0;
    top: 0;
    bottom: 0;
    justify-content: flex-start; }
  @include r(767) {
    left: 0;
    bottom: 0;
    padding: 20px;
    padding-top: 90px;
    padding-bottom: 67px;
    .img-lenta {
      display: block;
      margin-top: 46px; } }
  &.active {
    opacity: 1;
    display: flex;
    transform: translate(0, 0); }
  &__descr {
    @include h3;
    @include futura;
    margin-right: 20%;
    @include r(1023) {
      margin-right: 30px; }
    @include r(767) {
      margin-right: 0; } }
  .row {
    height: 100%; }
  .col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include r(1023) {
      display: block; }
    @include r(767) {
      display: block;
      height: auto; } }
  .row,
  &__bot {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @include r(767) {
      display: block;
      height: auto; } }
  &__bot {
    align-items: flex-end;
    @include r(1023) {
      margin-top: 40px; } }
  .info-file {
    color: #000;
    @include r(767) {
      margin-top: 54px;
      display: inline-block; }
    &__pic {
      font-size: 0;
      box-shadow: 0 0 8px rgba(0, 0, 0, .05);
      display: block;
      transition: .1s linear; }
    &:hover {
      .info-file {
        &__pic {
          box-shadow: 0 0 8px rgba(0, 0, 0, .1); }
        &__title {
          color: $orangeish_h; } } }
    &__title {
      display: block;
      margin-top: 13px;
      transition: .1s linear; }
    &__descr {
      display: block;
      color: #a8a8a8;
      margin-top: 6px; } }
  .close {
    position: absolute;
    top: 36px;
    left: 36px;
    cursor: pointer;
    @include r(767) {
      left: 20px; }
    svg {
      @include trans1; }
    &:hover {
      .icon {
        fill: $orangeish_h; } }
    &:active {
      .icon {
        fill: $orangeish_a; } } } }

.main__objects__review {
  cursor: pointer;
  @include trans1;
  &:hover {
    color: $orangeish_h; }
  &:active {
    color: $orangeish_a; } }

@include r(1023) {
  .main {
    &__home {
      height: auto; }
    &__screens {
      height: auto;
      min-height: 100vh; }
    &__screen {
      & .slider__dots {
        margin-top: -18px; }
      & .owl-dots {
        display: none; } } }
  .main__catalog {
    .slider {
      @include r(1023) {
        &__dots {
          margin-top: 5px !important; } }
      &__item {
        padding-top: 350px;
        &.rolls {
          @include r(1023) {
            padding-top: 50px;
            padding-bottom: 50px;
            margin-left: -20%;
            margin-right: -20%;
            img.pic-rolls {
              width: 100% !important;
              height: 100% !important;
              justify-content: flex-start; } }
          @include r(767) {
            padding: 0; } } }
      &__counter {
        display: none; }
      .owl-item {
        text-align: center;
        img {
          margin-left: 160px !important;
          width: 150% !important;
          object-position: 0;
          max-height: 100% !important; } } } } }

@include r(767) {
  .main__screen {
    .owl-carousel {
      overflow: hidden; }
    .owl {
      &-item {
        opacity: 1 !important; }
      &-nav {
        display: flex;
        position: absolute;
        top: 98px !important;
        left: 20px; }
      &-prev,
      &-next {
        position: static;
        width: 43px;
        svg {
          width: 100%; } } } }
  .main__objects {
    min-height: 0;
    height: auto;
    padding-top: 0;
    .slider {
      position: relative; }
    .container-wrapper {
      padding: 0; }
    .owl-carousel {
      position: static; }
    &__descr {
      margin-bottom: 50px; }
    &__review {
      margin-top: 62px;
      margin-bottom: 16px; } }
  .main__catalog {
    //min-height: 100vh
    height: auto;
    min-height: 614px;
    padding-top: 36px;
    padding-bottom: 32px;

    .main__screen__header {
      padding-bottom: 32px; }

    .owl-stage, .slider .owl-stage-outer {
      height: auto !important; }
    .slider {
      top: 0 !important;
      position: relative !important;
      padding-top: 58px;
      left: 0 !important;
      &__counter {
        display: flex;
        right: 0 !important;
        top: 0 !important; }
      .owl-carousel {
        height: auto;
        overflow: visible; }
      &__dots {
        display: block;
        position: static; }
      &__dot {
        display: none;
        text-decoration: none;
        &:before {
          display: none; }
        &.active {
          display: block; } }

      &__item {
        flex-direction: column;
        height: auto;
        padding-top: 0;
        justify-content: space-between;
        &-pic {
          padding-bottom: 0;
          img {
            min-width: 0 !important;
            display: block; } } }
      .owl-item {
        height: auto;
        opacity: 0 !important;
        &.active {
          opacity: 1 !important; }
        img {
          position: static !important;
          transform: translate(0, 0) !important;
          margin-left: 0;
          width: 100% !important;
          min-width: 0 !important;
          margin: 0 !important;
          margin-bottom: 36px !important;
          display: block; } }
      .btns {
        position: static;
        display: block;
        .btn-next__wrapper {
          margin-bottom: 20px;
          &:last-child {
            margin-bottom: 0; } } } }
    .owl {
      &-nav {
        left: 0;
        top: 0 !important; }
      &-carousel {
        position: static; } } }
  .slider__counter {
    top: 98px !important;
    bottom: auto !important;
    right: 20px !important;
    &__line {
      width: 30px;
      margin: 0 10px; } }
  .main__object__slider {
    display: none !important; } }

.main__bot {
  a {
    @include trans1;
    &:hover {
      opacity: .7; } } }

.mainBotLink {
  font-size: 24px;
  font-weight: normal;
  display: flex;
  align-items: center;
  @include trans1;

  svg {
    margin-left: 16px;
    width: 20px;
    height: 16px; } }

.mainBotInfo {
  width: calc(100% / 1.5);
  background-color: $sapphire;
  color: #fff;
  font-size: 30px;
  padding: 42px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include futura;
  font-weight: 500;

  @include small-viewport {
    width: 100%;
    flex: 1 0 100%; }

  .mainBotLink {
    color: #fff;
    @include proxima;
    margin-top: 40px; } }

.mainBotColumn {
  width: calc(100% / 3);
  background-color: #fff;
  color: #000;
  padding: 32px 32px 40px;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &:last-child {
    background-color: #f3f3f3;
    .mainBotLink,
    .mainBotColumnHeader {
      color: #000;

      svg {
        fill: #000; } } }

  a.mainBotLink {
    color: #000;
    display: block;
    margin-top: 30px;

    svg {
      fill: #000; } }

  .owl-carousel {
    position: static;
    margin: 0;
    display: block;
    z-index: 0; } }

.mainBotColumnInfo {
  flex: 0 1 auto;
  position: relative;
  overflow: hidden;
  height: 100%;
  max-height: 152px;

  &:after {
    pointer-events: none;
    content: '';
    @include absoluteFill;
    background-image: linear-gradient(bottom, #f3f3f3 0, rgba(0, 0, 0, 0) 100px); } }

.mainBotColumnHeader {
  font-size: 30px;
  margin-bottom: 16px;
  @include futura;
  font-weight: bold;
  overflow: hidden;
  display: block; }

.mainBotProductionSlider {
  .owl-dots {
    display: flex; }

  .owl-dot {
    width: 8px;
    height: 8px;
    background-color: #d8d8d8;
    margin-right: 12px;
    border-radius: 9px;
    margin-top: 25px;

    &:hover,
    &.active {
      background-color: $orangeish; } } }

.mainBotProductionSliderItem {
  font-size: 24px; }

@include small-viewport {
  .main__screen {
    flex-direction: row; }
  .main__bot {
    flex-wrap: wrap;
    height: 100vh;
    padding-top: 126px;
    padding-left: 0; }
  .main__hero {
    flex: 1 0 264px;
    height: auto;
    align-items: flex-end;

    .big-title {
      font-size: 14vh;
      writing-mode: vertical-lr;
      transform: rotate(-180deg) !important;
      padding-top: 32px;
      padding-right: 80px; } }

  .mainBotColumn {
    width: 50%; } }

@include r(1023) {
  .mainBotProductionSlider {
    .owl-dots {
      display: flex; } } }

@include tablet-viewport {
  .main__hero {
    height: calc(36vh); }

  .main__bot {
    padding-left: 96px;
    height: calc(64vh);
    flex-wrap: wrap; }

  .mainBotInfo {
    width: 100%;
    height: 50%; }

  .mainBotColumn {
    width: 50%;
    height: 50%; }

  .main__screen {
    .slider__dots {
      top: 220px; }
    &.main__catalog {
      .slider__dots {
        top: 0; } } } }

@include mobile-viewport {

  .main__screen {
    padding-top: 16px;

    &.main__services {
      min-height: 624px; } }

  .main__bot {
    display: block;
    height: auto;
    padding-left: 0; }

  .mainBotInfo,
  .mainBotColumn {
    width: 100%;
    padding: 32px 22px 44px 22px;
    font-size: 26px;
    min-height: 320px; }

  .mainBotProductionSliderItem {
    font-size: 16px; }

  .mainBotColumnInfo {
    min-height: 0;
    height: 140px; }

  .mainBotLink {
    font-size: 20px;
    svg {
      width: 16px;
      height: 13px;
      margin-left: 6px; } }
  .mainBotColumnHeader {
    font-size: 26px; }

  .main__screen.main__catalog {
    .slider__dot {
      &:after {
        display: none; } } } }

@include rmin(768) {
  .main__screen {
    .owl-nav {
      display: none; } } }
