.simpleFooter {
  padding-bottom: 30px;
  .container-wrapper {
    flex-direction: row !important;
    justify-content: space-between;
  }
}

@include mobile-viewport {
  .simpleFooter {
    font-size: 16px;

    .container-wrapper {
      display: block;
      padding: 0 20px;
    }
  }

  .simpleFooterCopyright {
    margin-bottom: 15px;
  }
}
