.search {
  @include hMLeft;
  @include hMTop;
  @include cMRight;
  @include cPBot;
  @include r(767) {
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 130px; }
  &__input {
    height: 86px;
    border-bottom: solid 1px rgba(0, 0, 0, 0.3);
    width: 100%;
    padding-right: 44px;
    &-wrapper {
      position: relative;
      margin-bottom: 47px;
      margin-top: -25px; }
    @include trans1;
    &:hover {
      border-color: rgba(0, 0, 0, 0.4); }
    &:focus {
      border-color: $orangeish_a; } }
  &_button {
    background-color: transparent;
    font-size: 24px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    .icon {
      stroke: #000;
      @include trans1; }
    &:hover {
      .icon {
        stroke: $orangeish_h; } } }
  &__result {
    &__item {
      margin-bottom: 64px;
      display: block;
      color: #000000;
      &:last-child {
        margin-bottom: 0; }
      @include trans1;
      &:hover {
        color: $orangeish_h; }
      &__title {
        @include h3;
        margin-bottom: 20px; } } } }

