.shaped {
  &__hero {
    h1 {
      br {
        display: none; } } }
  &__imgs {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include r(767) {
      display: block; } }
  &__img {
    flex: 1 0 25%;
    @include r(1023) {
      flex: 1 0 50%; }
    @include r(767) {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0; } }
    img {
      width: 100%; } }
  &:last-child {
    .hr {
      display: none; } }
  &-accessories {
    color: #ffffff;
    position: relative;
    @include r(767) {
      margin-top: 65px; }
    &__title {
      position: absolute;
      z-index: 2;
      @include cLeft;
      top: 75px;
      @include r(1023) {
        top: 40px; }
      @include r(767) {
        top: -31px;
        transform: translate(0, -100%);
        color: #000000; } } }

  &-slider {
    &__item {
      &__title {
        position: absolute;
        left: 50%;
        bottom: 175px;
        @include h3;
        @include r(1023) {
          bottom: 50px; }
        @include r(767) {
          bottom: 28px;
          left: 20px; } } }
    @include owlNavFlex;
    .owl {
      &-prev {
        @include cLeft; }
      &-next {
        left: auto;
        @include cRight; }
      @include r(767) {
        &-nav {
          top: 18px !important; } } }

    @include r(1023) {
      .slider__counter {
        top: 62px; } }
    @include r(767) {
      .slider__counter {
        top: 20px !important;
        right: 20px; } } } }
