.production-about-wrapper {
  padding: 160px 0;
  background-size: cover;
  background-position: center;
  margin-bottom: 70px;
  position: relative;
}

.production-about {
  max-width: 628px;
  background-color: #fff;
  padding: 64px 50px 92px;
  position: relative;
  z-index: 2;

  .icon-gost {
    font-size: 50px;
    margin-right: 20px;
  }

  .article__gost {
    display: flex;
    align-items: center;
  }
}

.production-about-title {
  font-size: 72px;
  margin-bottom: 54px;
}

.production-about-description {
  font-size: 24px;
  margin-bottom: 50px;

  p {
    margin: 0 0 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.production-about-image {
  width: 100%;
  height: 100%;
  @include absoluteFill;
  object-fit: cover;
}

@media only screen and (max-width: 1439px) {
  .production-about-image {
    height: auto;
    position: static;
  }

  .production-about-wrapper {
    padding: 0;
  }

  .production-about {
    padding: 44px 0;
    max-width: 434px;
    box-sizing: content-box;
    margin: 0 auto;
  }

  .production-about-title {
    font-size: 56px;
  }
}

@include tablet-viewport {
  .production-about-title {
    margin-bottom: 32px;
  }

  .production-about-description {
    margin-bottom: 35px;
  }
}


@include mobile-viewport {
  .production-about {
    padding: 45px 0;

    .article__gost {
      display: block;

      .icon-gost {
        margin-bottom: 6px;
      }
    }
  }

  .production-about-title {
    font-size: 36px;
    margin-bottom: 25px;
  }

  .production-about-description {
    font-size: 20px;
    margin-bottom: 26px;
  }
}
