.certificates {
  display: flex;
  flex-wrap: wrap;
  margin-top: -1px;
  $b: 1px solid #e8e8e8;
  border: $b;
  @include r(767) {
    display: block;
    border: 0; }
  .cell {
    flex: 1 0 50%;
    padding: 50px;
    max-width: 50%;
    border: {
      bottom: $b;
      right: $b; }
    &:nth-child(2n) {
      border-right: 0; }
    &:nth-last-child(2),
    &:last-child {
      border-bottom: 0; }
    //width: 50%
    @include r(1023) {
      flex: 1 0 100%;
      max-width: 100%; }
    @include r(767) {
      border-right: 0 !important;
      border-bottom: $b !important;
      padding: 38px 0; }
    img {
      width: 100%; } }
  &.small {
    .cell {
      flex: 1 0 25%;
      &:nth-child(2n) {
        border-right: $b; }
      &:nth-child(4n) {
        border-right: 0; }
      @include r(1023) {
        flex: 1 0 50%;
        &:nth-child(2n) {
          border-right: 0; } } }
    &:nth-last-child(4),
    &:nth-last-child(3),
    &:nth-last-child(2),
    &:last-child {
      border-bottom: 0; } }


  &__slider {
    position: relative;
    .owl {
      &-dots {
        display: flex;
        justify-content: center;
        position: absolute;
        bottom: -12px;
        left: 0;
        right: 0; }
      &-dot {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #D8D8D8;
        margin-right: 17px;
        @include trans1;
        &:hover {
          background-color: $sapphire_h; }
        &.active {
          background-color: $sapphire_a; }
        &:last-child {
          margin-right: 0; } } } } }
.banner-lab {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 113px;
  background-color: $very-light-pink;
  position: relative;
  overflow: hidden;
  @include r(767) {
    margin: -1px 0 0 0;
    max-width: none !important;
    padding: 60px 20px 80px 20px !important;
    &.cell {
      border-bottom: 0 !important; } }
  img {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    max-width: 100%;
    max-height: 80%;
    width: auto !important;
    transform: translate(30%, 0);
    @include r(1023) {
      max-height: 100%;
      transform: translate(0); }
    @include r(767) {
      transform: translate(50%, 0);
      max-height: 80%; } }


  &__title {
    @include h2;
    @include r(1023) {
      max-width: 50%; }
    @include r(767) {
      max-width: 210px; } } }
.banner-growth {
  background-color: $very-light-pink;
  @include h2;
  @include r(1023) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 10px !important;
    .link-next {
      transform: translate(0, -100%);
      margin-top: 0; } }
  @include r(767) {
    margin: 0;
    margin-top: -1px;
    max-width: none !important;
    padding: 30px 20px !important;
    display: block;
    &.cell {
      border-bottom: 0 !important; }
    .link-next {
      transform: none; } }
  &__title {
    @include r(1023) {
      flex: 0 1 50%; } }
  &__descr {
    display: flex;
    margin-top: 134px;
    @include r(1023) {
      flex-direction: column;
      flex: 0 1 30%;
      margin-top: 0;
      br {
        display: none; }
      .big-title {
        font-size: 220px;
        margin-top: -40px; } }
    @include r(767) {
      display: flex;
      flex-direction: row;
      margin: 50px 0 35px 0;
      &__right {
        flex: 0 1 190px; }
      .big-title {
        font-size: 110px; } }

    sup {
      @include h4;
      margin-left: -.5em;
      vertical-align: super;
      @include r(1023) {
        font-size: 15px; } }
    &__right {
      margin-left: 50px;
      @include r(1023) {
        margin-left: 0; } } } }


