.news {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px 0 -20px;
  overflow: hidden;
  @include r(1023) {
    display: block;
    margin: 0; }
  .form-subscribe {
    margin: 0 20px 50px 20px;
    width: calc(50% - 40px);
    padding: 65px 40px;
    flex-direction: column;
    align-items: flex-start;
    @include r(1439) {
      padding: 40px;
      &__right {
        display: block;
        .form__field {
          width: 100%;
          margin-bottom: 20px;
          margin-top: -20px; }
        .input {
          width: 100%; } } }
    @include r(1023) {
      width: 100%;
      margin: 0 0 45px 0;
      .h3 {
        margin-bottom: 43px; }

      &__descr {
        margin-bottom: 30px; } }
    @include r(767) {
      padding: 40px 20px;
      .h3 {
        margin-bottom: 32px; } }

    &__right {
      width: 100%; }
    .btn {
      min-width: 0;
      width: 100%;
      flex: 0 1 45%; }
    input {
      width: 100%;
      min-width: 220px;
      flex: 0 1 55%;
      padding-right: 40px; } } }

.new {
  margin: 0 20px 50px 20px;
  //flex: 0 1 50%
  width: calc(50% - 40px);
  max-width: 50%;
  color: #000;
  transform: translate(0, 0);
  @include r(1023) {
    width: 100%;
    max-width: none;
    margin: 0 0 45px 0;
    display: block; }
  @include r(767) {
    &:last-child {
      margin-bottom: 0; } }
  &__info {
    position: relative; }
  &:hover {
    color: $orangeish_h; }
  &-date {
    @include h4;
    display: block;
    margin-bottom: 10px; }
  &__pic {
    height: 424px;
    margin-bottom: 30px;
    transition: .4s;
    &-wrapper {
      margin-bottom: 30px;
      overflow: hidden;
      .new__pic {
        margin-bottom: 0; } }
    &-inner {
      width: 100%;
      height: 100%; }
    @include bgcc;
    @include r(767) {
      height: 192px; } }
  &:hover {
    .new {
      &__pic {
        transform: scale(1.1); } } } }

.new-hero {
  height: 424px;
  display: flex;
  align-items: flex-end;
  padding: 46px 36px;
  color: #fff !important;
  position: relative;
  overflow: hidden;
  @include r(1023) {
    display: flex; }
  @include r(767) {
    height: 192px;
    padding: 17px; }
  .new {
    &__pic {
      @include absoluteFill; }
    &__info {
      transition: .4s; } }
  @include bgcc; }

.new-page {
  @include r(767) {
    padding-top: 0; }
  &__hero {
    @include r(767) {
      height: auto;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .page__hero__title {
        position: static;
        padding: 254px 20px 42px 20px; } }

    .socials {
      margin-top: 90px; }

    .social {
      margin-right: 22px;
      @include trans1;
      svg {
        fill: $sapphire !important; }

      &:hover {
        opacity: .7; } }
    & .main__news {
      &__info {
        margin: 0;
        position: static;
        max-width: 840px;
        padding-right: 0; }
      &__date {
        @include proxima;
        font-weight: normal; } } }
  &__content {
    max-width: 640px;
    margin: 70px auto;
    @include r(767) {
      margin: 30px 0; }
    p {
      &:last-child {
        margin-bottom: 0; } }
    @include r(1439) {
      padding-left: 160px;
      max-width: 840px; }
    @include r(1023) {
      padding-left: 170px; }
    @include r(767) {
      padding-left: 0; }
    &:first-child {
      margin-top: 0; }
    @include h4;
    .new-page__title {
      @include h3;
      margin-bottom: 45px;
      @include r(767) {
        margin-bottom: 30px; } }
    &-wrapper {
      position: relative; } }
  &__party {
    font-size: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    @include cLeft;
    @include r(1023) {
      max-width: 140px;
      img {
        max-width: 115px; } }
    @include r(767) {
      position: static;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 60px;
      img {
        margin-right: 20px; } }

    &__descr {
      margin-top: 30px;
      font-size: 16px;
      @include r(767) {
        margin-top: 0;
        width: 100%;
        flex: 1 0 auto; } } }
  &__img {
    max-width: 950px;
    margin: 0 auto;
    img {
      width: 100%; }
    &__descr {
      margin-top: 17px;
      font-size: 16px;
      font-style: italic;
      color: #989898; } }
  .form-subscribe {
    margin-top: 100px;
    @include r(1023) {
      display: block;
      .h3 {
        margin-bottom: 35px; } } } }

.new__slider {
  position: relative;
  margin-bottom: 80px;
  @include r(767) {
    margin-bottom: 48px; }
  .slider__counter {
    color: #000;
    position: static;
    &__line {
      background-color: #000; } }
  &__item {
    background-color: #fff;
    &__bot {
      display: flex;
      align-items: center;
      margin-top: 30px;
      @include r(767) {
        align-items: flex-start;
        margin-top: 14px; }
      .new-page__img__descr {
        margin: 0 0 0 28px; } } }
  img {
    width: 100%; }
  .owl {
    &-nav {
      position: absolute;
      top: auto;
      left: auto;
      @include cRight;
      @include r(767) {
        display: none; } } } }

@include r(767) {
  .news {
    & + .btn-more-wrapper {
      margin-top: 45px; } }
  .other-news {
    .link-next {
      margin-top: 50px; } } }

.main__news__info {
  .main__news {
    @include r(1023) {
      position: absolute;
      bottom: auto; }
    @include r(767) {
      min-height: 0; }
    &__info {
      position: absolute;
      @include cBot;
      @include cLeft;
      @include cRight;
      max-width: 1164px;
      padding-right: 200px;
      @include r(1023) {
        padding-right: 0; } }
    &__date {
      @include h4;
      margin-bottom: 20px;
      display: block; } } }
