.find-cost {
  background-color: $sapphire;
  padding: 50px 0;
  color: #ffffff;
  height: 100vh;
  min-height: 900px;
  display: flex;
  flex-direction: column;
  @include r(1023) {
    padding-bottom: 70px;
    height: auto;
    min-height: 100vh; }
  @include r(767) {
    padding: 30px 0; }
  &__nav {
    display: flex;
    .btn-next {
      &__icon {
        margin-right: 0; } } }
  &-prev {
    & .btn-next {
      &__icon {
        background-color: transparent !important;
        transform: rotate(180deg);
        margin-right: 10px;
        &:hover {
          opacity: .7; } } } }
  &-prev,
  &-next {
    margin-right: 0; }
  &__header {
    position: absolute;
    top: 130px;
    @include cRight;
    display: flex;
    @include r(1023) {
      flex-direction: column;
      align-items: flex-end;
      .slider__counter {
        top: 140px;
        margin-right: 0;
        margin-bottom: 14px; } }
    @include r(767) {
      flex-direction: row;
      top: 100px;
      justify-content: space-between;
      left: 20px;
      right: 20px; } }
  .slider__counter {
    color: #ffffff;
    position: static;
    margin-right: 90px;
    @include r(1439) {
      margin-right: 30px; }
    &__line {
      background-color: #fff; } }
  .container {
    flex: auto;
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%; }
  .section__title {
    text-align: center;
    margin-bottom: 50px;
    @include r(1439) {
      margin-bottom: 72px; }
    @include r(1023) {
      margin-bottom: 60px; }
    @include r(767) {
      text-align: left;
      margin-bottom: 30px; } }
  &__items {
    display: flex;
    flex-direction: column;
    flex: auto;
    justify-content: center;
    @include r(767) {
      margin-top: 70px; } }
  &__item {
    display: none;
    flex: auto;
    &__info {
      @include h4;
      @include r(1023) {
        display: none; } }
    &__header {
      @include h3;
      padding-right: 320px;
      @include r(1439) {
        padding-right: 280px; }
      @include r(1023) {
        padding-right: 120px; }
      @include r(767) {
        padding-right: 0; } }
    &.active {
      display: flex;
      flex-direction: column;
      justify-content: space-between; }
    &__inner {
      flex: auto;
      justify-content: space-between;
      align-items: center;
      display: flex;
      @include r(1023) {
        flex-direction: column;
        padding-top: 120px; }
      @include r(767) {
        padding-top: 40px; } }
    &__left,
    &__right {
      padding: 0 40px;
      flex: 0 1 50%;
      @include r(1023) {
        padding: 15px 0; } }
    &__left {
      padding-left: 0;
      @include r(1023) {
        width: 100%;
        padding-bottom: 0;
        padding-top: 0; } }
    &__right {
      padding-right: 0;
      display: flex;
      justify-content: center;
      @include r(1023) {
        padding-bottom: 0;
        flex: auto;
        align-items: center;
        width: 85%; }
      @include r(767) {
        display: none; } }
    &__imgs {
      font-size: 0;
      img {
        flex: 0 1 100%;
        max-width: 100%;
        max-height: 426px;
        @include r(1023) {
          max-height: 252px;
          object-fit: cover;
          width: 100%;
          &:nth-child(1) {
            max-height: 200px; } } } } }

  &__actions {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px 20px -10px;
    @include r(1023) {
      margin-bottom: 0; }
    @include r(767) {
      margin-left: -5px;
      margin-right: -5px; } }
  &__label {
    flex: 1 0 calc(20% - 20px);
    margin: 0 10px 20px 10px;
    @include r(767) {
      flex: 1 0 calc(100% - 10px) !important;
      margin: 0 5px 10px 5px; }
    span {
      height: 92px;
      border: solid 1px #ffffff;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      cursor: pointer;
      text-align: center;
      padding: 0 20px;
      @include r(767) {
        padding: 0 5px; }
      @include trans1;
      @include h4;
      @include r(767) {
        height: 62px; } }
    input {
      font-size: 0;
      height: 0;
      width: 0;
      position: absolute;
      &:checked {
        & + span {
          background-color: $orangeish;
          border-color: $orangeish; } } } }

  .slide {
    &1 {
      & .find-cost__label {
        flex: 1 0 calc(20% - 20px);
        @include r(767) {
          flex: 1 0 calc(50% - 10px) !important; } } }
    &2 {
      & .find-cost__label {
        flex: 1 0 calc(50% - 20px); } }
    &6 {
      & .find-cost__label {
        flex: 1 0 calc(50% - 20px);
        &:nth-last-child(1),
        &:nth-last-child(2) {
          margin-bottom: 0;
          @include r(767) {
            margin-bottom: 10px; } } } }
    @include r(1023) {
      &5 {
        & .find-cost__item__inner {
          align-items: flex-start; }
        & .find-cost__item__right {
          display: block; }
        & .find-cost__area {
          display: block; } } }
    @include r(767) {
      &5 {
        & .find-cost__item__right {
          display: block; }
        & .find-cost__area {
          display: block; } } } }


  .form__field {
    &__title {
      mw {} } }
  &__area {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: left;
    @include r(1023) {
      &-calculator {
        @include r(767);
        .input {
          width: 320px; }
        @include r(767) {
          .form__field {
            width: 100%;
            .input {
              width: 100%; } } } } }

    sup {
      font-size: .3em;
      vertical-align: super;
      margin-left: -1em; }

    &__header {
      margin-bottom: 20px;
      @include h4; }
    .h2 {
      margin-bottom: 20px;
      * {
        display: inline-block; } } }
  .form {
    background-color: transparent;
    .form__fields {
      padding-top: 80px;
      @include r(1023) {
        padding-top: 0; } }
    @include r(767) {
      padding-bottom: 0; } }
  .input,
  .textarea {
    background-color: #fff; }
  .file-upload__header {
    color: #ffffff; }
  .icon-clip {
    fill: #fff; }
  .form__fields {
    width: 100%;
    margin-top: 0; }
  .panel {
    height: 350px;
    position: relative;
    @include r(1023) {
      width: 100%; }
    &__value {
      position: static;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      padding: 0 10px;
      @include h3; }
    div,
    svg {
      @include absoluteFill; }
    .panel__top {
      bottom: auto;
      width: 100%; }
    .panel__bottom {
      top: auto;
      width: 100%; }
    .panel__left {
      right: auto; }
    .panel__right {
      left: auto; }
    div {
      &.panel__top,
      &.panel__bottom {
        height: 1px;
        background-color: rgba(227, 227, 227, 0.3);
        &.orange {
          background-color: $orangeish;
          height: 8px; } }
      &.panel__left,
      &.panel__right {
        width: 1px;
        background-color: rgba(227, 227, 227, 0.3);
        &.orange {
          background-color: $orangeish;
          width: 8px; } } }
    .orange,
    svg {
      z-index: 2; }
    &.thickness {
      svg {
        &.panel__top.orange {
          top: -4px; } } } }
  .form__policy {
    color: #ffffff; } }
