@mixin absoluteFill {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto; }

@mixin hoverOpacity {
  transition: .1s linear;
  &:hover {
    opacity: .7; }
  &:last-child {
    margin-right: 0; } }
@mixin bgcc {
  background: {
    position: center;
    size: cover;
    repeat: no-repeat; } }
@mixin mPT {
  padding-top: 120px; }

//header & container indents
@mixin hTop {
  top: 120px;
  @include r(1023) {
    top: 214px; }
  @include r(767) {
    top: 90px; } }
@mixin hTop2 {
  top: 120px;
  @include r(1023) {
    top: 110px; }
  @include r(767) {
    top: 90px; } }
@mixin headerLeft {
  left: 400px;
  @include r(1439) {
    left: 276px; }
  @include r(1023) {
    left: 200px; }
  @include r(767) {
    left: 20px; } }
@mixin cLeft {
  left: 80px;
  @include r(1439) {
    left: 40px; }
  @include r(1023) {
    left: 30px; }
  @include r(767) {
    left: 20px; } }
@mixin cRight {
  right: 80px;
  @include r(1439) {
    right: 40px; }
  @include r(1023) {
    right: 30px; }
  @include r(767) {
    right: 20px; } }
@mixin cBot {
  bottom: 50px;
  @include r(1439) {
    bottom: 40px; }
  @include r(1023) {
    bottom: 38px; } }

@mixin hMTop {
  margin-top: 150px;
  @include r(1023) {
    margin-top: 125px; }
  @include r(767) {
    margin-top: 90px; } }
@mixin hMLeft {
  margin-left: 400px;
  @include r(1439) {
    margin-left: 276px; }
  @include r(1023) {
    margin-left: 170px; } }
@mixin cMRight {
  margin-right: 80px; }
@mixin cPBot {
  padding-bottom: 100px; }
@mixin cMLeftIn {
  margin-left: 320px;
  @include r(1439) {
    margin-left: 236px; }
  @include r(1023) {
    margin-left: 170px; } }

//
@mixin trans {
  transition-duration: .4s;
  transition-timing-function: ease; }
@mixin trans1 {
  transition: .1s linear; }
@mixin op1tt00 {
  opacity: 1;
  transform: translate(0,0); }


@mixin owlNavFlex {
  .owl {
    &-nav {
      position: absolute;
      display: flex; } } }
