.headerTitle {
  @include futura;
  font-size: 110px;
  text-align: center;
  line-height: normal;
  padding: 100px 0 60px;

  @include small-viewport {
    font-size: 72px;
  }

  @include tablet-viewport {
    font-size: 72px;
  }
}

@include mobile-viewport {
  .headerTitle {
    font-size: 48px;
    text-align: left;
    padding-bottom: 40px;
  }

  .siteNavigation {
    & ~ div .headerTitle {
      padding-top: 140px;
    }

    ~ .headerTitle {
      padding-top: 140px;
    }
  }
}
