.customSlider-wrapper {
  position: relative;

  .closeGallery {
    display: none;
  }

  .owl-nav {
    height: 64px;
    position: absolute;
    left: auto;
    top: auto;
    right: 32px;
    bottom: 64px;
    display: flex;
    align-items: center;
  }

  .owl-dots {
    display: none;
  }

  .owl-next,
  .owl-prev {
    width: 54px;
    height: 54px;
    background: $sapphire;
    @include trans1;
    border-radius: 99px;
    position: relative;

    //noinspection CssUnknownTarget
    &:after {
      content: '';
      background: url('/icons/rightWhite.svg') no-repeat center;
      @include absoluteFill;
      left: 2px;
    }

    &:hover {
      background-color: $sapphire_h;
    }

    &:active {
      background-color: $sapphire_a;
    }
  }

  .owl-prev {
    transform: rotate(180deg);
  }
}

.customSlider-item {
  padding-top: 60%;
  background-size: cover;
  background-position: center;
}

.customSlider-dots {
  display: flex;
  position: absolute;
  left: 36px;
  bottom: 64px;
  z-index: 1;
}

.customSlider-more,
.customSlider-dot {
  flex: 0 0 100px;
  height: 64px;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  @include trans1;

  &:not(:last-child) {
    margin-right: 10px;
  }

  &.hidden {
    opacity: 0;
    visibility: hidden;
    display: none;
  }

  &.active {
    opacity: .5;
  }

  &:hover {
    opacity: .8;
  }
}

.customSlider-more {
  background-color: #fff;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@include tablet-viewport {
  .customSlider-dots {
    bottom: 20px;
    left: 20px;
  }

  .customSlider-dot {
    flex: 0 0 80px;
    height: 54px;
  }

  .customSlider {
    .owl-nav {
      bottom: 20px;
      right: 20px;
    }
  }
}

@include mobile-viewport {
  .customSlider-dots {
    display: none;
  }

  .customSlider-wrapper {
    padding-bottom: 80px;
    position: relative;

    .owl-nav {
      top: auto !important;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      justify-content: center;
    }
  }

  .customSlider {
    position: static;
  }
}
