.modal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  min-height: 100vh;
  padding-top: 100px;
  display: none;
  z-index: 10;
  opacity: 0;
  @include r(1023) {
    padding-top: 70px; }
  @include r(767) {
    padding-top: 50px; }
  &.active {
    display: block; }
  &-background {
    @include absoluteFill;
    position: fixed;
    background-color: rgba(#000, 0.7);
    display: none;
    opacity: 0;
    &.active {
      display: block; } }


  &-locations {
    color: #ffffff;
    line-height: 1;
    right: auto;
    height: auto;
    position: absolute;
    min-height: 0;
    bottom: auto;
    ul {
      display: inline-block;
      li {
        margin-bottom: 36px; } }

    .h-loc {
      cursor: pointer;
      display: inline-block;
      @include trans1;
      &:hover {
        color: $orangeish_h; }
      &.active,
      &:active {
        color: $orangeish_a; }
      &.active {
        cursor: default; } } }
  &.active {
    opacity: 1;
    display: flex; } }
