.constantFooter {
  padding: 34px 0;
  background-color: #fff;

  .container {
    display: flex;
    justify-content: space-between;
  }
}

.constantFooter-copyright,
.constantFooter-policy,
.constantFooter-yarovoy {
  opacity: .3;
  color: #000;
  text-decoration: none;
}

@include small-viewport {

  .constantFooter-copyright {
    max-width: 136px;
  }

  .constantFooter-policy {
    max-width: 186px;
  }

  .constantFooter-yarovoy {
    max-width: 206px;
  }
}

@include tablet-viewport {

  .constantFooter-copyright {
    max-width: 136px;
  }

  .constantFooter-policy {
    max-width: 186px;
  }

  .constantFooter-yarovoy {
    max-width: 206px;
  }
}

@include mobile-viewport {
  .constantFooter {
    .container {
      display: block;
    }
  }

  .constantFooter-copyright,
  .constantFooter-policy {
    margin-bottom: 20px;
  }

  .constantFooter-policy {
    max-width: 165px;
  }

  .constantFooter-yarovoy {
    max-width: 183px;
    display: block;
  }
}

