.servicePage {
  .btn {
    margin-top: 48px;
  }
}

.service-switcher {
  display: flex;
  margin-top: 50px;
}

.service-switcherItem {
  position: relative;
  font-size: 0;
  overflow: hidden;

  img {
    width: 100%;
    transition: .3s;
  }

  &:hover {
    img {
      transform: scale(1.1);
    }
  }
}

.service-switcherItemHeader {
  @include absoluteFill;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

@include mobile-viewport {
  .service-switcher {
    flex-direction: column;
  }
}
