.selectric-wrapper {
  position: relative;
  display: inline-block;
  cursor: pointer;
  text-align: left;
  transition: .1s linear border-color, color; }

.selectric-responsive {
  width: 100%; }

.selectric {
  width: 280px;
  height: 60px;
  @include h4;
  color: #DCDCDC;
  padding: 0 14px;
  border: solid 1px #e7e7e7;
  background-color: #fff;
  display: block;
  outline: none;
  appearance: none;
  position: relative;
  transition: .3s ease;


  &:hover,
  &-focus,
  &-open {
    .selectric {
      border-color: #a7c2f5;
      color: #bababa; } } }

.selectric:after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  $b: 3px solid transparent;
  border-left: $b;
  border-right: $b;
  border-top: 3px solid #000;

  right: 15px;
  top: 0;
  bottom: 0;
  margin: auto;
  transition: .15s linear; }


.selectric-wrapper.selectric-open .selectric:after {
  transform: rotate(180deg); }

.selectric .label {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 60px;
  user-select: none; }

.selectric .button {
  display: none; }

.selectric.active {
  border-color: #fff; }

.selectric .button:after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  border-top-color: #BBB;
  border-bottom: none; }

.selectric-focus .selectric {
  border-color: #cfcfcf; }

.selectric-hover .selectric {}
/*border-color: #c4c4c4*/

.selectric-hover .selectric .button {
  color: #a2a2a2; }

.selectric-hover .selectric .button:after {
  border-top-color: #a2a2a2; }

.selectric-open {
  z-index: 9999; }

.selectric-open .selectric {
  border-color: $orangeish; }

.selectric-disabled {
  cursor: default;
  .selectric {
    background-color: rgba(231, 231, 231, 0.24);
    border: solid 1px #e7e7e7; }

  //opacity: 0.5
  //cursor: default
  -webkit-user-select: none {
    -moz-user-select: none {
      -ms-user-select: none {
        user-select: none; } } } }

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0; }

.selectric-hide-select select {
  position: absolute;
  left: -100%; }

.selectric-hide-select.selectric-is-native {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10; }

.selectric-hide-select.selectric-is-native select {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border: none;
  z-index: 1;
  box-sizing: border-box;
  opacity: 0; }

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }

.selectric-open .selectric-items {
  display: block;
  opacity: 1;
  transition: .15s linear;
  height: auto; }
/* Items box */
.selectric-items {
  margin-top: 10px;
  opacity: 0;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #F8F8F8;
  z-index: -1;
  box-shadow: 0 0 10px -6px;
  overflow: hidden;
  border: 1px solid $orangeish;
  transition: .15s linear;
  height: 0;
  overflow: hidden; }

.selectric-items .selectric-scroll {
  height: 100%;
  overflow: auto;
  overflow: hidden; }

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%; }

.selectric-items ul, .selectric-items li {
  background: #fff;
  list-style: none;
  padding: 0;
  margin: 0;
  @include h4;
  line-height: 20px;
  min-height: 20px; }

.selectric-items li {
  display: block;
  color: #BABABA;
  cursor: pointer;
  padding: 20px 14px; }

.selectric-items li:last-child {
  overflow: hidden; }

.selectric-items li.selected {
  background: #fff;
  color: #fff; }

.selectric-items li.highlighted {
  background: $very-light-pink;
  color: #000; }

.selectric-items li:hover {
  background: $very-light-pink;
  color: #000; }

.selectric-items .disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default !important;
  background: none !important;
  color: rgba(255, 255, 255, .4) !important;
  -webkit-user-select: none {
    -moz-user-select: none {
      -ms-user-select: none {
        user-select: none; } } } }

.selectric-items .selectric-group .selectric-group-label {
  font-weight: bold;
  padding-left: 10px;
  cursor: default;
  -webkit-user-select: none {
    -moz-user-select: none {
      -ms-user-select: none {
        user-select: none; } } }
  background: none;
  color: #444; }

.selectric-items .selectric-group.disabled li {
  filter: alpha(opacity=100);
  opacity: 1; }

.selectric-items .selectric-group li {
  padding-left: 25px; }
