.openGallery {
  width: 54px;
  height: 54px;
  position: absolute;
  top: 120px;
  right: 80px;
  z-index: 3;
  border: 1px solid #fff;
  border-radius: 99px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;

  @include small-viewport {
    right: 40px;
  }

  @include tablet-viewport {
    top: 110px;
    right: 30px;
  }

  @include mobile-viewport {
    display: none;
  }
}
