
.sandwich-examples {
  display: flex;
  @include r(767) {
    display: block; }
  &__item {
    padding: 138px 0 60px 0;
    flex: 0 1 50%;
    overflow: hidden;
    @include r(1023) {
      padding: 74px 0 30px 0; }
    &:first-child {
      background-image: linear-gradient(199deg, #f7f7f7, #e8e8e8); }
    &:last-child {
      background-image: linear-gradient(198deg, #f9f9f9, #e7e7e7); }
    @include r(767) {
      padding-top: 35px;
      display: block; }
    &__pic {
      width: 100%;
      font-size: 0;
      padding-right: 63px;
      @include r(1023) {
        padding-right: 30px; }
      img {
        width: 100%;
        @include trans; } }
    &:hover {
      img {
        transform: scale(1.025); } }
    &__title {
      color: #000000;
      margin-top: 92px;
      display: inline-block;
      vertical-align: middle;
      margin-left: 60px;
      @include r(1023) {
        margin-top: 30px;
        margin-left: 30px; }
      @include trans1;
      .icon {
        transform: rotate(-90deg);
        font-size: 19px;
        vertical-align: middle;
        @include trans1; }
      @include h3;
      &:hover {
        color: $orangeish_h;
        .icon {
          fill: $orangeish_h; } }
      &:active {
        color: $orangeish_a;
        .icon {
          fill: $orangeish_a; } } }
    &.right {
      .sandwich-examples__item {
        &__pic {
          text-align: right;
          padding-right: 0;
          padding-left: 63px;
          @include r(1023) {
            padding-left: 30px; } }
        @include r(767) {
          &__pic {
            transform: scaleX(-1); } } } } } }
.mineral-wool {
  display: flex;
  &__pic {
    flex: 1 0 300px;
    max-width: 300px;
    position: relative;
    img {
      width: auto;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0; }
    @include r(767) {
      position: static;
      margin-bottom: 42px;
      &.not-mobile {
        display: none; }
      img {
        position: static;
        max-width: 100%;
        height: auto; } } }
  .container {
    width: 100%;
    flex: 1 1 auto; }
  &__title {
    margin-bottom: 50px;
    @include r(767) {
      margin-bottom: 40px; } }
  &__descr {
    margin-bottom: 60px;
    @include r(767) {
      font-size: 20px;
      @include proxima;
      font-weight: normal;
      line-height: 1.4;
      margin-bottom: 30px; } }
  &__items {
    display: flex;
    justify-content: space-between;
    @include r(1023) {
      display: block; } }
  &__item {
    flex: 0 1 300px;
    @include r(1023) {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } } } }
.sandwich__objects {
  padding-top: 60px !important;
  .slider__counter {
    top: 54px; }
  @include r(1023) {
    min-height: 1024px; }
  @include r(767) {
    min-height: 0; }
  .slider__item__info {
    padding-top: 50px;
    @include r(767) {
      padding-top: 100px; } } }
