.modal-menu {
  background-color: $sapphire;
  color: #ffffff;
  flex-direction: column;
  bottom: 0;
  min-height: 0;
  opacity: 0;
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: transparent;

  a {
    color: #ffffff;
    transition: .1s;

    &:hover {
      opacity: .7;
    }
  }

  &:before {
    content: '';
    border-bottom: solid 1px rgba(255, 255, 255, 0.2);
    position: absolute;
    left: 0px;
    right: 0px;
    top: 80px;

    @include r(1023) {
      top: 70px;
    }

    @include r(767) {
      top: 50px;
    }
  }

  .container {
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: space-between;
  }

  .footer {
    display: block;

    .container {
      flex-direction: row;
    }
  }

  .social {
    background-color: rgba(125, 143, 199, 0.2);
  }
}

.modal-menu__inner {
  height: 100%;
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
}

.modal-menu__scroller {
  display: flex;
  flex-direction: column;
  flex: auto;
  overflow-y: auto;
}

.modal-menu-links {
  flex: 1 0 auto;
  justify-content: flex-start;
  padding-top: 46px;
}

.modal-menu-group {
  display: flex;
  align-items: center;
  padding: 24px 0;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }
}

.modal-menu-groupName {
  font-size: 24px;
  flex: 0 0 234px;
  width: 100%;
  padding-right: 24px;
}

.modal-menu-groupLinks {
  flex: 0 1 auto;
  width: 100%;
  margin-top: -20px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.modal-menu-link {
  margin: 20px 40px 0 0;
  display: inline-block;
}

.modal-menu-contacts {
  .modal-menu-link {
    margin: 0 0 0 64px;
  }
}

@include mobile-viewport {
  .modal {
    .container {
      display: block;
      width: 100%;
    }
  }

  .modal-menu-links {
    padding-top: 0;
  }

  .modal-menu-group {
    display: block;
  }

  .modal-menu-groupName {
    font-size: 20px;
    margin-bottom: 18px;
    display: inline-block;
  }

  .modal-menu-groupLinks {
    display: block;
  }

  .modal-menu-link {
    display: block;
  }

  .modal-menu-contacts {
    .modal-menu-link {
      margin: 16px 0;
    }
  }
}
